.cards-auto {
  border-radius: 4px;
  border: 1px solid @lightGrey;
  height: 160px;
  .lg-block({
    height: 140px;
  });

  .md-block({
    height: auto;
    // min-height: 455px;
  });
}

.cards-auto__inner {
  display: flex;
  align-items: center;
  height: 100%;
  padding-right: 24px;

  .md-block({
    flex-direction: column;
    align-items: stretch;
    padding: 0 16px;
    padding-bottom: 24px;
    // height: 100%;
  });
}

.cards-auto__image {
  margin-right: auto;
  // width: 229px;
  flex-basis: 229px;
  align-self: stretch;
  flex-shrink: 0;
  overflow: hidden;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;

  img {
    width: 100%;
    height: auto;
  }

  .lg-block({
    flex-basis: 140px;
    margin-right: 24px;
  });

  .md-block({
    flex-basis: 120px;
    margin: 0 -16px;
    border-bottom-left-radius: 0;
    border-top-right-radius: 4px;
  });

}

.cards-auto__content {
  box-sizing: border-box;
  margin-left: 24px;
  margin-right: 24px;
  padding-top: 24px;
  padding-bottom: 24px;
  align-self: stretch;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .lg-block({
    padding-bottom: 16px;
    padding-top: 16px;
    margin-right: auto;
  });

  .md-block({
    // min-height: 243px;
    display: block;
    margin: 0;
    margin-bottom: 24px;
    padding: 24px 0;
    border-bottom: 1px solid @lightGrey;
  });
}

.cards-auto__title {
  margin-bottom: 16px;

  .md-block({
    font-size: 24px;
  });
}

.cards-auto__stats {
  display: flex;
  align-items: center;
  .lg-block({
    flex-wrap: wrap;
    height: 100%;
  });
  // justify-content: space-between;
  .md-block({
    display: flex;
    margin-bottom: 16px;
  });
}

.cards-auto__tags {
  .lg-block({
    flex-basis: 100%;
    margin-bottom: auto;
  });

  .md-block({
    margin-bottom: 16px;
  });
}

.cards-auto__pass-bag {
  padding: 0 15px;
  margin-left: 16px;
  margin-right: 16px;
  border-left: 1px solid @lightGrey;
  border-right: 1px solid @lightGrey;

  .lg-block({
    margin-left: 0;
    padding-left: 0;
    border-left: 0;
  });

  & .pass-bag__pass,
  & .pass-bag__bag {
    .md-block({
      font-size: 16px;
      line-height: 16px;
      &::before {
        width: 20px;
        height: 20px;
      }
    });
  }

}

.cards-auto__text {
  margin-top: auto;
  font-size: 12px;
  font-weight: 400;
  color: @lightDark;
  overflow: hidden;
  .lg-block({
    display: none;
  });

  .md-block({
    margin-top: 16px;
    padding-top: 16px;
    display: block;
    font-size: 14px;
    overflow: visible;
  })
}

.cards-auto__icons {
  display: flex;
  margin: -4px;
  padding: 4px;

  li {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 4px;
    width: 16px;
    height: 16px;


    svg {
      width: 16px;
      height: auto;
    }

    .md-block({
      margin: 7px;
      width: 16px;
      height: 16px;
      svg {
        width: 100%;
        height: auto;
      }
    });
  }

}

.cards-auto__price-block {
  margin-left: auto;
  .md-block({
    width: 100%;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    margin: 0;

    .btn-price__price {
      margin-bottom: 0;
      padding-right: 16px;
    }
  });
}
