

.add-passengers__title {
  margin-bottom: 16px;
}

.add-passengers {
  width: 248px;

  label {
    width: 100%;
    margin-bottom: 12px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    &:last-child {
      align-items: flex-start;
    }

    & > span:first-of-type {
      margin-left: auto;
    }

    p {
      font-size: 16px;
      font-weight: 400;
      color: @dark;

      span {
        display: block;
        font-size: 14px;
        font-family: "Roboto Condensed", Arial, Helvetica, sans-serif;
        font-weight: 400;
        color: @grey;
      }
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  input[type="number"] {
    display: block;
    width: 2em;
    text-align: center;
    padding: 0;
    margin: 0;
    border: none;

  }
}
