.info-auto__wrapper {
  display: flex;
  align-items: flex-start;

  .lg-block({
    flex-direction: column;
    align-items: flex-start;
    box-sizing: border-box;
    width: 160px;
  });

  .md-block({
    width: auto;
    flex-direction: row;
    align-items: stretch;

    padding-bottom: 24px;
    border-bottom: 1px solid @lightGrey;
    margin-bottom: 24px;
  });
}

.info-auto__avatar {
  width: 64px;
  height: 64px;
  border-radius: 4px;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .lg-block({
    width: 48px;
    height: 48px;
  });

  .md-block({
    width: 96px;
    height: 96px;
    flex-shrink: 0;
    margin-right: 16px;
  });
}


.info-auto__name-wrapper {
  // align-self: flex-start;
  padding-left: 12px;

  .pass-bag {
    display: none;

    .md-block({
      display: flex;
      margin-top: 8px;
    });
  }

  .lg-block({
    padding-left: 0;
  });

  .md-block({
    display: block;
    box-sizing: border-box;
  });
}

.info-auto__auto-name {
  display: block;
  font-size: 14px;

  .lg-block({
    // text-align: center;
  });

  .md-block({
    display: block;
    height: auto;
    font-size: 18px;
    line-height: 1em;
    font-family: "Geometria", Arial, Helvetica, sans-serif;
    color: @dark;
  });
}

.info-auto__auto-stats {
  font-size: 14px;
  color: @grey;

  span {
    display: block;

    .lg-block({
      display: inline;
      padding-right: 12px;
    });
  }
  span:last-child {
    display: none;

    .md-block({
      display: inline;
    });
  }

  .lg-block({
    text-align: center;
    font-size: 12px;
  });
}

.info-auto__rating {
  display: block;
  margin: 0 auto;
  line-height: 17px;
  width: 37px;
  box-shadow: 0 0 0 1px red;

  .lg-block({
    display: none;
  });
}
