
.checkout__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 16px;
  border-bottom: 1px solid @lightGrey;

  font-size: 12px;
  color: @grey;

  & > p {
    width: 160px;
    margin-right: 24px;
  }

  & > p:nth-child(2) {
    line-height: 1em;

    .md-block({
      display: none;
    });
  }
}

.checkout__logo {
  margin-left: auto;
  margin-right: 32px;
}

.checkout__main {
  padding-top: 20px;
}

.checkout__title-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;

  margin-bottom: 19px;

  .md-block({
    align-items: flex-start;
    margin-bottom: 32px;
  });
}

.checkout__title {
  font-size: 24px;
}

.checkout__save-pdf {
  font-size: 12px;
  color: @lightDark;
  border-color: @lightGrey;

  .md-block({
    box-sizing: border-box;
    height: 40px;
    padding: 0;
    width: 160px;
    flex-shrink: 0;
  });
}

.checkout__fieldset {
  padding-bottom: 16px;
  margin-bottom: 23px;
  border-bottom: 1px solid @lightGrey;

  th {
    padding-top: 14px;
    font-size: 12px;
    color: @grey;
  }

  td {
    font-size: 14px;
    padding-right: 32px;
  }

  &--footer {
    border: none;
    padding-bottom: 0px;
    margin-bottom: 0;
  }

  &--table-mobile {
    display: none;
  }

  .md-block({
    &--table-mobile {
      display: block;
    }
    &--table-desktop {
      display: none;
    }
  });
}

.checkout__subtitle--additional,
.checkout__subtitle--footer {
  margin-bottom: 14px;
}

.checkout__additional-params {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-height: 130px;
  font-size: 12px;
  font-weight: 400;

  .md-block({
    flex-wrap: nowrap;
    max-height: none;
  });
}

.checkout__total {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 14px;
}

.checkout__calculate {
  font-size: 12px;
  color: @grey;
}

.checkout__sum {
  padding-left: 16px;
  font-size: 18px;
  font-weight: 500;
}

.checkout-page__to-lk {
  align-self: flex-end;
}

