
.page-multiselector {
  display: flex;
  height: auto;
  line-height: auto;

  li:first-child span {
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
  }
  li:last-child span {
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
  }

  .md-block({
    width: 100%;
    li {
      flex-grow: 1;
    }
  });
}

.page-multiselector__radio {
  font-size: 12px;
  line-height: 14px;
  font-weight: 500;

  span {
    box-sizing: border-box;
    display: block;
    text-align:  center;
    padding-top: 9px;
    padding-bottom: 8px;
    padding-left: 16px;
    padding-right: 16px;
    border: 1px solid @lightGrey;
    color: @grey;
    background-color: #ffffff;
    width: 176px;

    .md-block({
      width: auto;
    });
  }

  input:checked + span {
    color: #ffffff;
    border-color: @green;
    background-color: @green;
  }
}


.page-multiselector-2 {
  display: flex;
  height: auto;
  line-height: auto;

  .md-block({
    width: 100%;
    li {
      flex-grow: 1;
    }
  });
}

.page-multiselector-2__radio {
  font-size: 14px;
  line-height: 14px;
  font-weight: 500;

  span {
    box-sizing: border-box;
    display: block;
    text-align:  center;
    padding-top: 9px;
    padding-bottom: 9px;
    padding-left: 16px;
    padding-right: 16px;
    border-bottom: 1px solid @lightGrey;
    color: @grey;
    width: 100%;

    .md-block({
      width: auto;
    });
  }

  input:checked + span {
    color: @green;
    padding-bottom: 8px;
    border-bottom: 2px solid @green;

  }
}
