
.pass-bag {
  width: 72px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.pass-bag__pass {
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  color: @lightDark;
  display: flex;
  align-items: center;

  &::before {
    content: "";
    display: inline-block;
    width: 18px;
    height: 18px;
    background-image: url("../img/icon-users-solid.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}

.pass-bag__bag {
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  color: @lightDark;
  display: flex;
  align-items: center;

  &::before {
    content: "";
    display: inline-block;
    width: 18px;
    height: 18px;
    background-image: url("../img/icon-baggage.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}
