.search-form__inner {
  width: 100%;
  max-width: 478px;
  background-color: @white;
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  box-sizing: border-box;

  .lg-block({
    margin: 0 auto;
  });
}

.search-form__form {
  position: relative;

  padding-bottom: 65px;
}

.search-form__title {
  padding: 48px 32px 32px 32px;

  &--mobile {
    display: none;
  }

  .md-block({
    &--desktop {
      display: none;
    }
    &--mobile {
      display: block;
    }
  })
}

.search-form__multiselector {
  margin: 0 32px;
  .md-block({
    margin: 0 16px;
  });
}

.search-form__multiselector-rad  {
  margin-bottom: 32px;
  justify-content: center;
  li {
    width: 50%;
  }
}

.search-form__destinations {
  position: relative;
  width: 100%;
  margin-bottom: 24px;
  display: flex;
  flex-direction: column;

  div:first-child {
    margin-bottom: 8px;
  }

  input {
    width: 100%;
  }

  .search-form__toggle {
    position: absolute;
    top: calc(50% - 16px);
    right: 24px;
    z-index: 2;
  }
}

.search-form__date {
  display: flex;
  flex-direction: column;
  margin-bottom: 8px;
}

.search-form__submit {
  display: block;
  box-sizing: border-box;
  height: 48px;
  width: 224px;
  margin-top: 41px;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  bottom: -24px;
  left: calc(50% - 112px);
}


.search-form__form--rent {
  .search-form__destinations {
    margin-bottom: 16px;
  }
  .search-form__date {
    margin-bottom: 16px;
    div:first-child {
      margin-bottom: 16px;
    }
  }

}


