.header-drop-list {
  // position: absolute;
  background-color: transparent;
  box-shadow: none;

  &::before,
  &::after {
    content: "";
    display: block;
    position: absolute;
    width: 10px;
    height: 10px;
    transform: rotate(45deg);
    background-color: #ffffff;
    top: -5px;
    right: 30px;


  }

  &::before {
    z-index: 3;
    border: 1px solid @lightGrey;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.08);
  }
  &::after {
    z-index: 6;
  }

  .field__drop-inner {
    position: relative;
    background-color: #ffffff;
    z-index: 5;
  }

  &--v2 {
    .field__drop-inner {
      width: 408px;
      height: 140px;
    }
  }



}
