.lk-content {
  position: relative;
  box-sizing: border-box;
  padding: 64px 40px;
  // margin: 0 auto;


  &--1-1 {
    padding-bottom: 20px;
  }
  &--2-1 {
    padding-bottom: 20px;
  }

  .lg-block({
    // width: calc(100% - 64px);
    padding: 48px 32px;
  });

  .md-block({
    // display: none;
    // width: 100%;
    padding: 0 17px 48px 15px;
  });

}


.lk-content__wrapper {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  // div {
    // width: 100px;
    // height: 100px;
    // background-color: #ffffff;
  // }

  .checkout-page {
    margin: 0 auto;
  }
}

.lk-content .auto-info .auto-info__inner {
  .lg-block({
    padding: 0;
  });
}

.lk-content__title-mobile {
  display: none;

  & > span {
    display: block;
  }
  & > .rating-widget {
    font-size: 14px;
    display: flex;
    align-items: center;

    .rating-widget__value {
      padding-right: 4px;
    }

    .profile-raty {
      height: 16px;
      display: flex;
      align-items: center;
      img {
        height: 14px;
        width: auto;
      }
    }
  }
  .md-block({
    padding: 32px 0;
    display: block;
  });
}
