
.rating-widget {
  color: @green;
  // font-size: 14px;
  font-weight: 500;
  display: flex;
  align-items: center;
}

.rating-widget__value {
  padding-right: 16px;
}

.rating-widget__star {
  width: 13.5px;
  height: 12px;
  display: block;
  margin-right: 4px;
  background-image: url("../img/star-solid.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
