
.service-popup {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;

  background-color: transparent;
  transform: translate3d(0, -30px, 0);
  display: block;
  opacity: 0;
  transition-duration: 0.5s;
  pointer-events: none;

  &.active {
    // pointer-events: auto;
    transform: translate3d(0, 0, 0);
    opacity: 1;
    transition-delay: 350ms;
    transition-duration: 0.5s;

    & > .service-popup__container {
      pointer-events: auto;
    }
  }

  .popup__close {
    right: 24px;
    top: 24px;
  }
}

.service-popup__container {
  position: relative;
  box-sizing: border-box;
  margin: auto;
  margin-top: 20vh;
  width: 95%;
  max-width: 472px;
  min-height: 254px;
  padding: 56px 32px 48px 32px;
  background-color: #ffffff;
  border-radius: 4px;
  border: 1px solid @lightGrey;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}

.service-popup__title {
  text-align: center;
  margin-bottom: 16px;
}

.service-popup__text {
  text-align: center;
  font-size: 16px;
  margin-bottom: 32px;
}

.service-popup__buttons {
  display: flex;

  .button {
    min-width: 160px;
  }

  .button:nth-child(2) {
    margin-left: 24px;
  }
}

.service-popup__close {
  border-color: @lightGrey;
  span {
    color: @lightDark;
  }
}


#popupRatingThnx {
  .service-popup__container svg {
    margin-top: -8px;
    margin-bottom: 16px;
  }
}


#popupRating {
  .service-popup__title {
    margin-bottom: 24px;
  }

  .service-popup__ratings {
    margin-bottom: 48px;
  }

  .service-popup__rating-row {
    display: flex;
    align-items: center;
    width: 234px;
    &:not(:last-child) {
      margin-bottom: 17px;
    }

    & > span {
      margin-right: auto;
    }
  }
}
