
.driver-request-auto__list {

  .driver-transfer-card__content {
    padding: 16px;
    justify-content: space-between;
  }

  .info-auto {
    width: 224px;
    margin-right: 24px;
    align-self: flex-start;
    margin-top: 8px;

    .md-block({
      width: 100%;
      margin-right: 0;
      margin-top: 24px;
    });
  }

  .info-auto__wrapper {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: flex-start;


    .info-auto__avatar {
      width: 48px;
      height: 48px;
      flex-shrink: 0;
      margin-right: 16px;

      .md-block({
        width: 96px;
        height: 96px;
      });
    }

  }

  .driver-transfer-card__button {
    margin-right: 0;
    .md-block({
      padding-top: 0;
      margin-left: 0;
      width: auto;
      display: flex;
      justify-content: flex-end;
      align-items: center;
    });
  }

  .driver-transfer-card__price-375 {

    & > span:first-child {
      display: block;
      font-size: 14px;
      font-weight: 500;
      color: @grey;
      margin-bottom: 5px;
    }

    & > span:last-child {
      padding-right: 16px;
    }
  }

  .driver-transfer-card__auto-data {
    margin-left: auto;
    margin-right: auto;
    display: flex;
    &::before {
      content: "";
      display: block;
      margin-right: 16px;
      height: 88px;
      width: 1px;
      background-color: @lightGrey;
    }
    & > div {
      display: table;
      font-size: 14px;
      & > div {
        display: table-row;
        & > span {
          display: table-cell;
          font-size: 12px;
          color: @grey;
          &:first-child {
            padding-right: 25px;
          }
        }
        &:nth-child(2n) > span {
          font-size: 14px;
          color: @lightDark;
          font-weight: 500;
          padding-bottom: 4px;

          & > span:last-child {
            font-size: 12px;
            color: @grey;
            font-weight: 400;
          }
        }
      }
    }

    .md-block({
      display: none;
    });
  }
}
