
.map-radius-popup {
  box-sizing: border-box;
  top: 64px;
  height: 748px;
  width: 91.4%;
  max-width: 520px;
  margin-left: 4.3%;
  z-index: 10000;

  .popup__close {
    position: absolute;
    right: 0;
    top: -36px;
  }
}

.map-radius-popup__container {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;

  background-color: @background;

}

.map-radius-popup__map {
  height: 636px;

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }

}

.map-radius-popup__pin {
  display: block;
  position: absolute;
  width: 280px;
  height: 280px;
  top: 120px;
  left: calc(50% - 140px);
  border-radius: 50%;
  border: 2px solid @green;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("../img/iconfinder_pin_green.svg");
}

.map-radius-popup__button-container {
  padding: 32px;
  display: flex;
  flex-direction: column;
  background-color: @background;
}
