.view-transf-popup {
  box-sizing: border-box;
  margin-top: 44px;
  width: 736px;


  .popup__close {
    position: absolute;
    top: 24px;
    right: 24px;
  }

  .lg-block({
    border-radius: 4px;
    width: 640px;
    margin-top: 96px;
    margin-left: 96px;
    margin-bottom: 48px;
    .popup__close {
      top: -36px;
      right: 0;
    }
  });
}



.view-transf-popup__container {
  box-sizing: border-box;
  position: relative;
  background-color: #ffffff;
  padding: 31px 48px 48px 48px;

  .lg-block({
    padding: 32px;
    border-radius: 4px;
  });
}

.view-transf-popup__title {
  margin-bottom: 24px;
}

.view-transf-popup__wrapper-top {
  padding-bottom: 11px;
  // display: flex;

  // & > div:first-child {
  //   margin-right: 32px;
  // }
}

.view-transf-popup__wrapper-data {
  display: flex;

  & > div:first-child {
    margin-right: 32px;
  }
}

.view-transf-popup__inner-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 22px;
}

.view-transf-popup__from-to {
  & > span {
    display: block;
  }
}

.view-transf-popup__address {
  margin-bottom: 8px;
}
.view-transf-popup__data {
  color: @grey;
}

.view-transf-popup__accept {
  display: block;
  box-sizing: border-box;
  width: 220px;
}

.view-transf-popup__hide {
  margin-right: 22px;
}


.view-transf-popup__table {
  display: table;
  margin-bottom: 22px;
}

.view-transf-popup__table-row {
  display: table-row;

  &:first-child {
    font-size: 14px;
    color: @dark;
    font-weight: 500;
  }
  &:not(:first-child) {
    font-size: 16px;
    color: @lightDark;
    font-weight: 400;
  }
}

.view-transf-popup__table-cell {
  position: relative;
  display: table-cell;
  line-height: 19px;
  &:not(:last-child) {
    padding-right: 32px;
  }
  &:first-child {
    padding-right: 32px;
    padding-bottom: 8px;
  }

  .tag-status {
    position: absolute;
  }

}

.view-transf-popup__places-list {
  position: absolute;
  display: flex;
}


.view-transf-popup__wrapper-map h3 {
  margin-bottom: 16px;
}

.view-transf-popup__map {
  height: 281px;
  position: relative;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.view-transf-popup__map-footer {
  width: 100%;
  background-color: @background;
  height: 48px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  padding: 0 48px;

  .view-transf-popup__table {
    margin: 0;
  }

  .view-transf-popup__table-row:first-child > div {
    font-size: 12px;
    color: @grey;
    padding-bottom: 4px;
  }
  .view-transf-popup__table-row:last-child > div {
    font-size: 14px;
    color: @lightDark;
  }
}

.view-transf-popup__map-pin {
  display: block;
  position: absolute;
  width: 24px;
  height: 24px;
  background-image: url("../img/iconfinder_pin_green.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &--from {
    left: 131px;
    top: 93px;
  }
  &--to {
    right: 156px;
    top: 110px;
  }
}


// view-rent-popup

#view-rent-popup {

  .view-transf-popup__from-to {
    font-weight: 500;
  }

  .view-transf-popup__auto-name {
    display: block;
  }
  .view-transf-popup__prop {
    display: flex;
    align-items: center;
    span {
      margin-right: 16px;
      color: @grey;
    }
  }

  .view-transf-popup__table {
    &--1,
    &--2 {
      .view-transf-popup__table-cell:first-child {
        width: 288px;

        .lg-block({
          width: 200px;
        });
      }
    }
  }
}
