.field {
  display: flex;
  flex-direction: column;
  position: relative;
  // width: 100%;
  & > input {
    width: 100%;
    box-sizing: border-box;
    height: 48px;
    border-radius: 4px;
    border: 1px solid @lightGrey;
    margin: 0;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 15px;
    padding-bottom: 14px;

    font-size: 16px !important;
    font-family: "Roboto", Arial, Helvetica, sans-serif;
    line-height: 19px;
    font-weight: 400;
    color: @lightDark;
  }

  & > textarea {
    width: 100%;
    box-sizing: border-box;
    height: 111px;
    border-radius: 4px;
    border: 1px solid @lightGrey;
    margin: 0;
    padding: 16px 32px;
    resize: none;

    font-size: 16px;
    line-height: 19px;
    font-weight: 400;
    color: @lightDark;

    & ~ span {
      position: absolute;
      bottom: 4px;
      right: 8px;
      font-size: 12px;
      font-weight: 400;
      color: @grey;
    }
  }

  & > label {
    font-size: 14px;
    color: @dark;
    font-weight: 500;
    margin-bottom: 8px;
  }

}

.field--icon {
  & > input {
    padding-left: 40px;

    // padding: 15px 40px 14px 40px;
  }
  & > input ~ span {
    display: block;
    width: 20px;
    height: 20px;
    position: absolute;
    left: 16px;
    bottom: 14px;
    background-position: center;
    background-repeat: no-repeat;
    opacity: 0.6;
    pointer-events: none;
  }

  & > input.datepicker ~ span {
    background-image: url("../img/icon-calendar-alt-regular.svg");
  }
  & > input.timepicker ~ span {
    background-image: url("../img/icon-waiting.svg");
  }

  & > input.address-from ~ span {
    background-image: url("../img/icon-direct-from.svg");
  }
  & > input.address-to ~ span {
    background-image: url("../img/icon-direct-to.svg");
  }
  & > input.address-where ~ span {
    background-image: url("../img/icon-pin-map.svg");
  }
  & > input.passengers-cout ~ span {
    background-image: url("../img/icon-users-company.svg");
  }

  & > input.ui-search ~ span {
    background-image: url("../img/icon-search.svg");
  }

  & > input.ui-email ~ span {
    background-image: url("../img/emai-icon.svg");
    opacity: 1;
  }
  & > input.ui-password ~ span {
    background-image: url("../img/pass-icon.svg");
    opacity: 1;
  }

}

.field--addit-icon {
  & > input {
    padding-right: 25px;
  }
  &::after {
    content: "";
    display: block;
    position: absolute;
    bottom: 14px;
    right: 10px;
    width: 20px;
    height: 20px;
    background-position: center;
    background-repeat: no-repeat;

    pointer-events: none;

    &--eye {
      background-image: url("../img/show_hide-icon.svg");
    }
  }
}

.field--drop {
  & > input {
      padding-right: 25px;
    }

  &::after {
    content: "";
    display: block;
    position: absolute;
    bottom: 14px;
    right: 10px;
    width: 20px;
    height: 20px;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url("../img/icon-arrow-down.svg");
    pointer-events: none;
  }
}

// Выпадающее окошко

.field--drop-list {
	cursor: pointer;
}

.field--drop-block {
  position: relative;
  cursor: pointer;
}

.field__dropdown-contain {
  // display: block;
  box-sizing: border-box;
  min-width: 100%;
  height: auto;
  // height: 140px;
	position: absolute;
	z-index: 2;
	top: -30000px;

}

.field__drop-out {
  box-sizing: border-box;
	position: relative;
	margin-top: 0;
	opacity: 0;
	box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.08);

  transition: all 0.4s ease-out;

}

.field__drop-inner {
  border-radius: 4px;
  background-color: #ffffff;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.08);
}

.destinations-list__item {
  padding-right: 10px !important;

  span {
    display: flex;
    align-items: center;
  }

  span::before {
    content: "";
    display: block;
    width: 16px;
    height: 16px;
    margin-right: 12px;
    background-position: center;
    background-repeat: no-repeat;
    background-image: url('../img/map-marker-alt-solid.svg');
  }

  &--airport {
    span::before {
      background-image: url('../img/plane-solid.svg');
    }
  }

  &--hotel {
    span::before {
      background-image: url('../img/hotel-solid.svg');
    }
  }
  &--architect {
    span::before {
      background-image: url('../img/architect-solid.svg');
    }
  }
}

.field--drop-pass {

  .field__dropdown-contain {
    min-width: 312px;
  }


  li:hover {
    background-color: #ffffff;
  }
}


// .field--drop-list input:focus + .field__dropdown-contain { top: 99%; }
// .field--drop-list input:focus + .field__dropdown-contain .field__drop-out { opacity: 1; }
.field--drop-list input:hover + .field__dropdown-contain { top: 99%;}
.field--drop-list input:hover + .field__dropdown-contain .field__drop-out {
  opacity: 1;
  margin-top: 8px;
}
.field__dropdown-contain:hover { top: 99%;}
.field__dropdown-contain:hover .field__drop-out {
  opacity: 1;
  margin-top: 8px;
}


.field--drop-block:hover > .field__dropdown-contain { top: 99%;}
.field--drop-block:hover > .field__dropdown-contain .field__drop-out {
  opacity: 1;
  margin-top: 30px;
}

