
.final-offer {
  box-sizing: border-box;
  background-color: @white;
  padding: 80px 0;

  .lg-block({
    padding: 64px 0;
  });

  .md-block({
    width: 100%;
    margin: 0;
  });
}

.final-offer__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  .md-block({
    max-width: 375px;
    width: 100%;
    margin: 0 auto;
  })
}

.final-offer__title {
  margin-bottom: 20px;
  font-size: 32px;
  font-weight: 500;
  color: @dark;
  text-align: center;

  .md-block({
    font-size: 24px;
  });
}

.final-offer__text {
  width: 544px;
  margin-bottom: 32px;
  font-size: 14px;
  font-weight: 400;
  text-align: center;
  color: @lightDark;

  .lg-block({
    width: 520px;
    margin-bottom: 24px;
  });
  .md-block({
    width: 375px;
  });
}

.final-offer__button {
  width: 224px;
}
