.faq-block {
  box-sizing: border-box;
  padding: 32px 48px;
  background-color: #fdfdfd;
  border-radius: 4px;
  border: 1px solid @lightGrey;
}

.faq-block__question {
  margin-bottom: 24px;
  font-size: 16px;
  font-weight: 500;
  color: @lightDark;
}

.faq-block__answer {
  font-size: 14px;
  color: @lightDark;
  font-weight: 400;
}
