
.request-transfer-table {
  box-sizing: border-box;
  padding: 32px;
  width: 100%;
  display: table;

  font-size: 14px;
  font-weight: 400;
  color: @lightDark;
}

.request-transfer-table__row {
  display: table-row;

  & > div {
    display: table-cell;
    padding-top: 30px;
  }

  &--head {
    font-weight: 500;
    color: @dark;

    & > div {
      padding-top: 0;
      padding-bottom: 16px;
      border-bottom: 1px solid @lightGrey;
    }
  }
}

.request-transfer-table__more {
  & a {
    height: 32px;
    width: 138px;
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.request-transfer-table__client {
  width: 192px;
  padding-right: 32px;
}

.request-transfer-table__from {
  width: 174px;
  padding-right: 32px;
}
