
.chat {
  width: 100%;
  // height: 80%;
  display: flex;
  justify-content: space-between;
  background-color: white;
  border-radius: 4px;
  border: 1px solid @lightGrey;
}

.chat--dialogs {
  .lg-block({
    .chat__chat-area {
      display: none;
    }
  });
}

.chat--chat {
  .lg-block({
    .chat__dialogs {
      display: none;
    }
  });
}

.chat__dialogs {
  box-sizing: border-box;
  width: 385px;
  flex-shrink: 0;
  padding-bottom: 43px;

  .ui-search {
    box-sizing: border-box;
    height: 48px;
    border-radius: 24px;
    &::placeholder {
      font-size: 14px;
      line-height: 14px;
      font-weight: 400;
    }
  }

  .lg-block({
    width: 100%;

    .ui-search {
      height: 36px;
      display: flex;
      align-items: center;
      padding: 0;
      padding-left: 40px;

      & ~ span {
        top: calc(50% - 10px);
      }
    }
  });
}

.chat__dialogs-head {
  padding: 24px 24px 16px 24px;

  & > div:first-child {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;

    button {
      border-bottom: none;
      position: relative;
      margin-right: 20px;
      &::after {
        content: "";
        display: block;
        position: absolute;
        width: 20px;
        height: 20px;
        right: -20px;
        top: calc(50% - 10px);
        background-image: url("../img/icon-arrow-down.svg");
        background-repeat: no-repeat;
        background-position: center;
      }
    }

    .lg-block({
      margin-bottom: 16px;
    });
  }
}

.chat__title {
  font-size: 20px;
  color: #000000;
}

.chat__list-wrapper {
  // height: 342px;
  margin-right: -16px;

  .lg-block({
    margin-right: 0;
  });

}

.chat__list {
  width: 100%;
  height: 342px;
  overflow-y: scroll;
  // overflow: auto;

  .lg-block({
    height: auto;
    overflow-y: auto;
  });
}

.chat__user {
  padding-top: 8px;
  padding-bottom: 10px;
  padding-left: 24px;
  padding-right: 25px;
  background-color: #ffffff;
  border-bottom: 1px solid @lightGrey;
  display: flex;
  &--active {
    background-color: @lightGrey;
  }
}

.chat__user-avatar {
  flex-shrink: 0;
  box-sizing: border-box;
  align-self: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 8px;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.chat__user-inner {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  & > div {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  h4 {
    font-size: 14px;
    font-family: "Roboto", Arial, Helvetica, sans-serif;
    line-height: 16px;
    font-weight: 400;
  }

  p {
    font-size: 12px;
    color: @lightDark;
  }



}
.chat__list-timeago {
  color: @grey;
}
.chat__message-cout {
  font-size: 12px;
  line-height: 14px;
  padding: 3px 5px;
  border-radius: 3px;
  background-color: @lightGrey;
  }

.chat__chat-area {
  width: 100%;
  z-index: 2;
  background-color: #ffffff;
  border-left: 1px solid @lightGrey;

  .lg-block({
    min-height: 509px;
    height: auto;
  });

  .md-block({
    min-height: 648px;
  });
}

.chat__stub {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  p {
    margin-top: 20px;
    max-width: 352px;
    font-size: 14px;
    text-align: center;
  }

  // .lg-block({
  //   display: none;
  // });
}

.chat__header {
  box-sizing: border-box;
  // height: 122px;
  padding: 21px 64px 14px 64px;
  border-bottom: 1px solid @lightGrey;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;

  .lg-block({
    padding: 21px 32px 14px 64px;
  });

  .md-block({
    padding: 21px 16px 16px 16px;
  });
}

.chat__header-img {
  width: 80px;
  height: 80px;
  overflow: hidden;
  border-radius: 50%;
  margin-right: 16px;

  .md-block({
    width: 48px;
    height: 48px;
  });

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.chat__header-info {
  display: flex;
  align-items: flex-start;
  flex-grow: 1;

  .md-block({
    flex-direction: column;
  });
}

.chat__header-view {
  margin-top: -8px;
  width: 100%;
  text-align: right;

  .button {
    font-size: 14px;
    font-weight: 400;
  }

  .md-block({
    margin-top: 19px;
    text-align: center;
  });
}

.chat__header-inner-1 {
  margin-right: 24px;
  p {
    width: 212px;
    font-size: 12px;

  }

  .md-block({
    margin-right: 0;
    p {
      display: none;
    }
  });
}

.chat__header-title {
  font-size: 16px;
  margin-bottom: 7px;
}

.chat-header-inner-2 {
  margin-left: auto;
  display: flex;
  flex-direction: column;
  align-items: center;

  .price-v1 {
    margin-bottom: 8px;
  }

  .auto-icons {
    box-sizing: border-box;
    display: flex;
    margin: -4px;
    li {
      box-sizing: border-box;
      width: 20px;
      height: 20px;
      margin: 4px;
    }
  }

  .md-block({
    flex-direction: row;
    margin-left: 0;
    width: 100%;

    .price-v1 {
      margin-bottom: 0;
      margin-right: auto;
      font-size: 14px;
    }
  });
}

.chat__messages-container {
  box-sizing: border-box;
  padding-left: 122px;
  padding-right: 64px;
  padding-bottom: 44px;
  padding-top: 23px;
  width: 100%;
  height: 306px;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  overflow: hidden;

  .lg-block({
    padding-left: 82px;
    padding-right: 48px;
  });

  .md-block({
    padding-right: 23px;
    padding-left: 20px;
    height: 445px;
  });
}

.chat__messages-list {
  margin-bottom: 12px;
  display: flex;
  flex-direction: column;
  // width: 100%;
}

.chat__message {
  position: relative;
  margin-bottom: 4px;
  max-width: 452px;
  display: inline-block;
  font-size: 14px;
  line-height: 22px;
  color: @lightDark;
}

.chat__messages-list--incoming {
  align-self: flex-start;
  .chat__message {
    padding: 8px 24px 8px 48px;
    margin-right: auto;
    border-top-right-radius: 10px;
    border-top-left-radius: 4px;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    background-color: #E8EBF0;
    p {
      position: relative;
      z-index: 101;
    }


    &--first::after {
      content: "";
      box-sizing: border-box;
      display: block;
      position: absolute;
      top: -25px;
      left: -10px;
      width: 100px;
      height: 100px;
      border: 50px solid transparent;
      border-top-color: #E8EBF0;
      transform: scaleY(0.5);
      pointer-events: none;
      z-index: 100;
    }
  }
}

.chat__message-avatar {
  position: absolute;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  overflow: hidden;
  top: -20px;
  left: -52px;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .md-block({
    display: none;
  });
}

.chat__message-username {
  position: absolute;
  left: 0;
  top: -25px;
  font-size: 12px;
  font-weight: 400;
  color: #000000;
}

.chat__messages-list--outgoing {
  align-self: flex-end;
  .chat__message {
    padding: 8px 48px 8px 24px;
    margin-left: auto;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
    border-bottom-right-radius: 4px;
    border-bottom-left-radius: 10px;
    background-color: #CCEEFF;

    p {
      position: relative;
      z-index: 101;
    }

    &--last::after {
      content: "";
      box-sizing: border-box;
      display: block;
      position: absolute;
      bottom: -25px;
      right: -10px;
      width: 100px;
      height: 100px;
      border: 50px solid transparent;
      border-bottom-color: #CCEEFF;
      transform: scaleY(0.5);
      pointer-events: none;
      z-index: 100;
    }
  }

  .md-block({
    margin-left: -10px;
  });
}

.chat__message-time {
  position: absolute;
  right: 8px;
  bottom: 4px;
  font-family: "Roboto Condensed", Arial, Helvetica, sans-serif;
  font-size: 12px;
  color: @grey;
  z-index: 105;
}

.chat__footer {
  height: 80px;
  padding-left: 64px;
  padding-right: 64px;
  background-color: @lightGrey;
  display: flex;
  align-items: center;

  .field {
    flex-grow: 1;
    margin-right: 32px;
    input {
      padding-left: 24px;
      padding-right: 24px;
      border-radius: 10px;
    }
  }

  .lg-block({
    padding: 0 24px;
  });

  .md-block({
    padding: 0 16px;

    .field {
      margin-right: 20px;
    }
  });
}

.chat__button-smile {
  position: absolute;
  right: 24px;
  top: calc(50% - 10px);
  width: 20px;
  height: 20px;
  border: none;
  padding: 0;
  background-image: url("../img/smile-regular.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.chat__button-send {
  border: none;
  box-sizing: border-box;
  width: 54px;
  height: 40px;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;

  svg {
    width: 24px;
    height: 24px;
    flex-shrink: 0;
  }
  span {
    font-family: "Roboto Condensed", Arial, Helvetica, sans-serif;
    font-size: 14px;
    font-weight: 400;
    color: @lightDark;
  }

  .md-block({
    width: 24px;
    height: 24px;
    span {
      display: none;
    }
  });

}

.chat__chat-area--unactive {
  .chat__stub {
    display: flex;
  }
  .chat__header {
    display: none;
  }
  .chat__messages-container {
    display: none;
  }
  .chat__footer {
    display: none;
  }
}

.chat__chat-area--active {
  .chat__stub {
    display: none;
  }
  .chat__header {
    display: flex;
  }
  .chat__messages-container {
    display: flex;
  }
  .chat__footer {
    display: flex;
  }
}
