.drop-list {
  box-sizing: border-box;
  display: block;
  width: 140px;
  height: 200px;
  border-radius: 4px;
  background-color: #ffffff;


}

.drop-list {
  border-radius:10px;
  background:#efefef;
  border:1px solid #777;
  position: absolute;
  opacity: 1;
  top: 200px;
  left: 500px;
}

.drop-list::before,
.drop-list::after {
  content: "";
  display: block;
  position: absolute;
  box-sizing: border-box;
  top: -11px;
  right: 20px;
  width:20px;
  height:20px;
  background:#ffffff;
  // z-index:-1;
  border:1px solid #777;
  transform: rotate(45deg);
  }

