.edit-auto-popup {
  margin-top: 56px;
  width: 928px;
  // height: 743px;
  border-radius: 4px;

  .popup__close {
    position: absolute;
    top: 34px;
    right: 34px;
  }

  .lg-block({
    width: 640px;
    margin-left: 96px;

    .popup__close {
      top: -36px;
      right: 0;
    }
  });

  // .md-block({
  //   margin-left: 0;
  //   width: 343px;
  // });
}



.edit-auto-popup__container {
  box-sizing: border-box;
  position: relative;
  border-radius: 4px;
  background-color: #ffffff;
  padding: 48px 64px;

  .lg-block({
    padding: 32px 24px;
  });
}

.edit-auto-popup__title {
  margin-bottom: 32px;
}

.edit-auto-popup__wrapper-data {
  display: block;

}

.edit-auto-popup__table {
  display: table;
}

.edit-auto-popup__table-row {
  display: table-row;

  &:first-child {
    font-size: 14px;
    color: @dark;
    font-weight: 500;
  }
  &:not(:first-child) {
    font-size: 16px;
    color: @lightDark;
    font-weight: 400;
  }
}

.edit-auto-popup__table-cell {
  position: relative;
  display: table-cell;
  line-height: 19px;
  &:not(:last-child) {
    padding-right: 32px;
  }
  &:first-child {
    padding-right: 32px;
  }

  .tag-status {
    position: absolute;
  }

}

.edit-auto-popup__places-list {
  position: absolute;
  display: flex;
}

.edit-auto-popup__image-list {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 32px;
  margin: -12px;
}

.edit-auto-popup__image-item {
  position: relative;
  margin: 12px;
  &--upload {
    label {
      width: 104px;
      height: 100px;
    }
  }
}

.edit-auto-popup__image-del {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  box-shadow: none;
  border: thin solid #ffffff;
  opacity: 0.3;
  top: 2px;
  right: 2px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  padding: 0;
  margin: 0;
  cursor: pointer;
  transform: rotate(45deg);
  transition-duration: 0.5s;
  &::before{
    content: "";
    position: absolute;
    left: calc(50% - 5px);
    top: calc(50% - 1px);
    display: block;
    width: 10px;
    height: 2px;
    background-color: #ffffff;
    transition-duration: 0.5s;
  }

  &::after {
    content: "";
    display: block;
    position: absolute;
    left: calc(50% - 1px);
    top: calc(50% - 5px);
    width: 2px;
    height: 10px;
    background-color: #ffffff;
    transition-duration: 0.5s;
  }

  &:hover {
    border-color: @red;
    opacity: 1;

    &::before,
    &::after {
      background-color: @red;
    }
  }
}

.edit-auto-popup__picture {
  width: 170px;
  height: 100px;

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .lg-block({
    width: 120px;
    height: 100px;
  });
}

.edit-auto-popup__image-footer {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  border: 1px solid @lightGrey;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 32px;

  font-size: 12px;

  &.mod-upload {
    border-color: transparent;
  }
}

.edit-auto-popup__inner-data {
  display: flex;
  justify-content: space-between;
  margin-right: 0;
  margin-bottom: 16px;

  & > .field {
    width: 46%;
  }
}

.edit-auto-popup__text {
  margin-top: 24px;
  margin-bottom: 32px;
}

.edit-auto-popup__footer {
  display: flex;
  justify-content: flex-end;

  .lg-block({
    justify-content: center;
  });
}

.edit-auto-popup__submit {
  width: 220px;
  box-sizing: border-box;
  margin-left: 24px;
}

.edit-auto-popup__reset {
  width: 160px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
}
