
#lk-edit-transf-popup {
  box-sizing: border-box;
  width: 77.4%;
  max-width: 1120px;
  border-radius: 4px;
  margin-top: 112px;
  margin-right: auto;
  margin-left: auto;
  background-color: #ffffff;
  border: 1px solid @lightGrey;

  .lg-block({
    width: 83.4%;
  });

  .md-block({
    margin-top: 64px;
    margin-right: 0;
    width: 100%;
  });

  .popup__close {
    right: 0;
    top: -36px;
  }
}

