.social-auth {
  box-sizing: border-box;
  width: 640px;
  // margin-left: 32px;
  padding: 32px 48px 48px 48px;
  border-radius: 4px;
  border: 1px solid @lightGrey;
  background-color: #ffffff;

  .field {
    min-width: 311px;
    width: 60%;
    margin-bottom: 16px;
  }

  .lg-block({
    // margin-left: 0;
  });

  .md-block({
    width: 100%;
    padding: 24px 16px 48px 16px;
  });
}

.social-auth__title {
  margin-bottom: 16px;
}

.social-auth__desc {
  color: @grey;
  font-size: 14px;
  margin-bottom: 24px;
}

.social-auth__account-button {
  color: @blue;
  border-bottom-color: @blue;
  margin-right: 47px;

}

.social-auth__list {
  margin-bottom: 48px;
}


.social-auth__item {
  display: flex;
  align-items: center;
  margin-bottom: 24px;

  &::before {
      content: "";
      display: inline-block;
      width: 24px;
      height: 24px;
      margin-right: 8px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }

  &--vk::before {
    background-image: url("../img/iconfinder_VK-65_3057656.svg");
  }
  &--fb::before {
    background-image: url("../img/iconfinder_facebook_317746.svg");
  }
  &--tw::before {
    background-image: url("../img/iconfinder_twitter_294655.svg");
  }
  &--google::before {
    background-image: url("../img/iconfinder_google_circle_294707.svg");
  }

  & > .social-auth__account-button {
    display: none;
  }
  &--active > .social-auth__account-button {
    display: block;
  }
}

.social-auth__submit {
  margin: 0 auto;
  display: block;
}
