.ui-tabs.multiselector {
  padding: 0;
  border: none;
}

.ui-widget.ui-widget-content.multiselector {
  border: none;

  & > ul {
    padding: 0;
    margin-bottom: 32px;
    border: none;
    background-color: transparent;
    display: flex;
  }

  & > ul > li {
    width: 50%;
    display: flex;
    justify-content: center;
    background-color: transparent;
    border: none;
    margin: 0;
    border-bottom: 2px solid @grey;

    a {
      font-size: 14px;
      font-weight: 500;
      line-height: 16px;
      padding-bottom: 11px;
      color: @grey;
    }

    &.ui-tabs-active {
      padding-bottom: 0;
      margin-bottom: 0;
      border-bottom-color:@green;

      a {
      color: @green;
      }
    }
  }

  .ui-tabs-panel {
    padding: 0;
  }
}
