
.checkout-page__inner {
  margin: 0 auto;
  width: 100%;
  max-width: 736px;
  display: flex;
  flex-direction: column;

  .lg-block({
    max-width: 704px;
    width: 100%;
  });

  .md-block({
    max-width: 520px;
    width: 100%;
  });
}

.checkout-page__title {
  display: flex;
  align-items: flex-start;
  margin-bottom: 32px;
  font-size: 24px;
  color: @green;

  svg {
    margin-right: 16px;
  }
}

.checkout-page__lk-desc {
  font-size: 14px;
  margin-bottom: 32px;

  .lg-block({
    display: none;
  });
}

.checkout-page__checkout {
  padding: 24px 64px;
  margin-bottom: 32px;
  background-color: #ffffff;
  border-radius: 4px;
  border: 1px solid @lightGrey;

  .md-block({
    padding: 24px 16px;
  });
}
