
.sign-popup {
  position: fixed;
  left: 224px;
  right: 224px;
  top: 110px;
  bottom: 110px;
  background-color: #ffffff;
  border-radius: 4px;
  transform: translate3d(0, -30px, 0);
  display: block;
  opacity: 0;
  transition-duration: 0.5s;
  pointer-events: none;

  .button {
    font-size: 14px;
  }

  .lg-block({
    left: 32px;
    right: 32px;
    top: 80px;
    bottom: 140px;
  });
  .md-block({
    left: 16px;
    right: 16px;
    top: 88px;
    bottom: auto;
    min-height: 530px;
    .button {
      font-size: 16px;
    }
  });

  .popup__close {
    right: 48px;
    top: 48px;

    .lg-block({
      right: 32px;
      top: 32px;
    });

    .md-block({
      right: 0;
      top: -36px;
    });
  }
  // &.show {
  //   display: block;
  // }

  &.active {
    pointer-events: auto;
    transform: translate3d(0, 0, 0);
    opacity: 1;
    transition-duration: 0.5s;
  }
}

.sign-popup__container {
  display: flex;
  height: 100%;
  width: 100%;


}

.sign-popup__aside {
  box-sizing: border-box;
  padding: 32px;
  flex-basis: 38.7%;
  background-color: #2F2F2F;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-image: url("../img/back-with-filter.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  h2 {
    color: #ffffff;
    margin-bottom: 16px;
    text-align: center;
  }

  p {
    font-size: 14px;
    text-align: center;
    margin-bottom: 24px;
  }

  .sign-popup__toggle-button {
    border-color: #ffffff;
  }

  .lg-block({
    flex-basis: 40.9%;
    padding: 16px;
  });

  .md-block({
    display: none;
  });
}

.sign-popup__main {
  position: relative;
  box-sizing: border-box;
  flex-basis: 61.3%;
  padding: 48px 128px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  // align-items: center;

  .sign-popup__toggle-button {
    display: none;
    align-self: center;
    border: none;
    padding: 0;
    width: auto;
    color: @grey;
    font-size: 14px;
    font-weight: 400;
    border-radius: 0;
    border-bottom: 1px solid @lightGrey;
    margin-bottom: 24px;

    .md-block({
      display: inline-block;
    });
  }

  .lg-block({
    flex-basis: 59.1%;
    padding: 32px;
  });

  .md-block({
    flex-basis: 100%;
    padding: 32px 16px;
    align-items: center;
    .sign-popup__toggle-button {
      display: inline;
      font-size: 14px;
    }
  });
}

.sign-popup__main > h2 {
  // text-align: left;
  margin-bottom: 16px;
  .md-block({
    text-align: center;
  });
}

.sign-popup__main > p {
  font-size: 14px;
  line-height: 22px;
  margin-bottom: 24px;
  .md-block({
    text-align: center;
  });

}

.sign-popup__form {
  align-self: stretch;
  display: flex;
  flex-direction: column;

  & > .field {
    margin-bottom: 16px;
  }

}

.sign-popup__checkbox {
  display: flex;
  align-items: center;
  margin-bottom: 24px;
  font-size: 14px;
  .custom-check {
    margin-right: 8px;
  }
}

.sign-in__submit {
  display: block;
  width: 224px;
  box-sizing: border-box;
  align-self: center;
  margin-bottom: 15px;
}

.sign-popup__forgot-password {
  align-self: center;
  margin-bottom: 47px;
}

.sign-popup__footer {
  align-self: center;
  width: 307px;
  display: flex;
  flex-direction: column;
  align-items: center;

  & > p {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16px;
    width: 100%;
    text-align: center;
    font-size: 12px;
    span {
      text-align: center;
    }

    &::before,
    &::after {
      content: "";
      display: block;
      height: 1px;
      background-color: @lightGrey;
      flex-basis: 32px;
    }
    }

}

.sign-popup__toggle-button {
  display: block;
  box-sizing: border-box;
  width: 220px;
  padding-left: 16px;
  padding-right: 16px;
  span {
    margin: 0;
  }
}

.sign-popup__user-type {
  margin-bottom: 23px;
}
