.sorting {
  display: flex;
  justify-content: flex-end;

  .field__dropdown-contain {
    left: -70px;

    li:last-child {
      border: none;
    }
  }
}

.sorting__title {
  margin-right: 16px;
  color: @grey;
  font-size: 16px;
  line-height: 1em;
}

.sorting__method {
  font-size: 16px;
  color: @dark;
  line-height: 1em;
}

.sorting__arrow {
  height: 18px;
}

