.history-rent {
  width: 100%;
  border-radius: 4px;
  border: 1px solid @lightGrey;
  background-color: #ffffff;
  padding: 32px 48px 48px 48px;

  .lg-block({
    padding: 24px 24px 48px 24px;
  });

  .md-block({
    padding: 0 16px 48px 16px;
    border: none;
  });

  .request-transfer-table__row > div {
    &:nth-child(2),
    &:nth-child(4),
    &:last-child {
      display: none;
    }

    &:last-child {
      text-align: right;
    }

    &:last-child a {
      display: inline;
      color: @blue;
      border-bottom-color: @blue;
    }

    .lg-block({
      &:nth-child(6),
      &:nth-child(8) {
        display: none;
      }

      &:last-child {
        display: table-cell;
      }
    });

    .md-block({
      &:first-child,
      &:nth-child(5) {
        display: none;
      }
      &:nth-child(2) {
        display: table-cell;
      }
    });
  }

  .request-transfer-table__more a {
    margin: 0 0 0 auto;
  }
}

.history-rent__auto {
  margin-bottom: 32px;
}

.history-rent__additional {
  display: none;

  .md-block({
    display: block;
    padding-bottom: 24px;
    border-bottom: 1px solid @lightGrey;
    margin-bottom: 32px;
  });
}

.history-rent__table {
  padding: 0;
}

.history-rent__table-title {
  display: none;

  .md-block({
    display: block;
    margin-bottom: 24px;
  });
}


.history-rent__to-statistic {
  display: none;

  .md-block({
    display: block;
    margin-top: 32px;
  });
}
