
.cards-direct {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 16px 32px;
  height: 160px;
  background-color: #ffffff;
  border-radius: 4px;
  border: 1px solid @lightGrey;

  .lg-block({
    padding: 16px;
  });

  .md-block({
    flex-direction: column;
    align-items: stretch;
    height: auto;
    padding: 26px 16px;
  });
}

.cards-direct__info-transfer {
  width: auto;
  height: 100%;
  padding: 16px 0;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;

  .md-block({
    height: auto;
    padding: 0;
    padding-bottom: 40px;
    border-bottom: 1px solid @lightGrey;
  });
}

.cards-direct__to-from {
  display: flex;
  height: 80px;
}

.cards-direct__time-date {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  font-size: 12px;
  line-height: 16px;
  color: @grey;

  .lg-block({
    & > span {
      position: relative;
    }
  });
}

.cards-direct__time-date-date {
  .lg-block({
    display: block;
  });
}

.cards-direct__time-date-time {
  .lg-block({
    position: absolute;
    right: 0;
    bottom: -16px;
  });
}

.cards-direct__icon-direct {
  height: 80px;
  display: flex;
  align-items: center;
  align-self: center;
}

.cards-direct__cities {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  font-size: 14px;
  line-height: 16px;
  color: @dark;
}

.cards-direct__places-left {
  display: none;
  color: @lightDark;
  font-size: 14px;
  padding-left: 15px;
  line-height: 1em;

  .md-block({
    display: inline-block;
    margin-bottom: 8px;
  });
}

.cards-direct__price-button {
  .price-v1 {
    margin-bottom: 9px;
    padding-right: 6px;
  }

  .md-block({
    order: 2;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 24px;

    .price-v1 {
      padding-right: 34px;
      margin-bottom: 0;
    }
  });
}

.cards-direct__button {
  box-sizing: border-box;
  width: 141px;
}

.cards-direct__info-driver {
  align-self: stretch;
  margin-left: 24px;
  padding-left: 24px;
  padding-top: 16px;
  padding-bottom: 16px;
  border-left: 1px solid @lightGrey;

  .md-block({
    border-left: none;
    border-bottom: 1px solid @lightGrey;
    margin-left: 0;
    padding-left: 0;
    display: flex;
    padding-top: 25px;
    padding-bottom: 24px;
  })
}

.cards-direct__driver {
  display: flex;
  align-items: center;
  box-sizing: border-box;

  .md-block({
    width: 180px;
    flex-grow: 1;
  });
}

.cards-direct__image-face {
  margin-right: 12px;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  img {
    display: block;
    width: 100%;
    height: auto;
  }
}

.cards-direct__rating {
  display: inline-block;
  text-align: center;
  color: @green;
  font-size: 14px;
  line-height: 1em;
  font-weight: 500;
  margin-bottom: 5px;

  .md-block({
    display: none;
  });
}

.cards-direct__driver-name {
  display: block;
  color: @lightDark;
  font-size: 14px;
}

.cards-direct__drivers-car {
  display: block;
  font-size: 12px;
  color: @grey;
}

.cards-direct__icons {
  align-self: flex-start;
  width: 100px;
  display: flex;
  justify-content: space-between;

  .md-block({
    width: auto;
  });
}
