
.profile-nav {
  width: 256px;
  flex-shrink: 0;
}

.profile-nav__field {

  &:not(:last-child) {
    margin-bottom: 16px;
  }
}

.profile-nav__title {
  padding: 8px 24px;
  background-color: @lightGrey;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  font-weight: 500;
  font-size: 14px;
  color: @dark;
}

.profile-nav__list {
  padding: 0 16px;
  border: 1px solid @lightGrey;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  background-color: #ffffff;

}

.profile-nav__item {
  padding: 12px 8px;

  a {
    display: block;
    font-size: 16px;
    color: @lightDark;
    text-decoration: none;
  }

  &--active a,
  &:hover a {
    color: @green;
  }

  &:not(:last-child) {
    border-bottom: 1px solid @lightGrey;
  }
}

.profile-nav--desktop-only {
  margin-right: 32px;
  .lg-block({
    display: none;
  });
}

.profile-nav--responsive {
  .lg-block({
    width: 100%;


    .profile-nav__item {
      position: relative;
      &::after {
        content: "";
        display: inline-block;
        position: absolute;
        width: 12px;
        height: 24px;
        right: 0;
        top: calc(50% - 12px);
        background-repeat: no-repeat;
        background-position: center;
        background-image: url("../img/icon-next-dark.svg");
      }
    }
  });
}
