.profile-rating {
  box-sizing: border-box;
  width: 640px;
  // margin-left: 32px;
  padding: 32px 48px 48px 48px;
  border-radius: 4px;
  border: 1px solid @lightGrey;
  background-color: #ffffff;


  .lg-block({
    // margin-left: 0;
  });

  .md-block({
    width: 100%;
    padding: 24px 16px 48px 16px;
  });
}

.profile-rating__title {
  margin-bottom: 19px;
}

.profile-rating__totals {
  display: flex;
  align-items: center;
  width: 100%;
  padding-bottom: 27px;
  margin-bottom: 24px;
  border-bottom: 1px solid @lightGrey;
}

.profile-rating__stars {
  width: 150px;
  height: 22px;
  background-color: red;
  margin-right: 24px;
}

.profile-rating__cout-review {
  font-size: 16px;
  color: @dark;
}

.profile-rating__review-item:not(:last-child) {
  margin-bottom: 16px;
}
