
.input {
  display: flex;
  flex-direction: column;
}

.input--select {
  position: relative;
  &::after {
    content: "";
    display: block;
    position: absolute;
    width: 20px;
    height: 20px;
    background-image: url("../img/icon-arrow-down.svg");
    background-repeat: no-repeat;
    background-position: center;
    right: 14px;
    top: calc(50% - 10px);
    cursor: pointer;
  }

  & > input {
    &::placeholder {
      color: @grey;
      // font-weight: 400;
    }
  }
}

