
.options__item {
  position: relative;
  padding-left: 33px;
  margin-bottom: 13px;

  font-size: 16px;
  line-height: 19px;


  &--conditioner .options__icon {
    background-image: url("../img/icon-conditioner.svg");
    }

  &--music .options__icon {
    background-image: url("../img/icon-music.svg");
    }
  &--pets .options__icon {
    background-image: url("../img/icon-pets.svg");
    }
  &--no-pets .options__icon {
    background-image: url("../img/icon-NOpets.svg");
    }
  &--no-smoking .options__icon {
    background-image: url("../img/icon-NOsmoking.svg");
    }
  &--smoking .options__icon {
    background-image: url("../img/icon-smoking.svg");
    }
}

.options__icon {
  display: block;
  position: absolute;
  width: 24px;
  height: 24px;
  // background-color: red;
  left: 0;
  top: calc(50% - 12px);
  background-position: center;
  background-repeat: no-repeat;
}
