.info-driver__wrapper {
  display: flex;
  align-items: flex-start;

  .lg-block({
    flex-direction: column;
    align-items: center;
    box-sizing: border-box;
    width: 114px;
  });
}

.info-driver__avatar {
  width: 64px;
  height: 64px;
  border-radius: 50%;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .lg-block({
    width: 48px;
    height: 48px;
  });
}


.info-driver__name-wrapper {
  // align-self: flex-start;
  padding-left: 12px;

  .lg-block({
    padding-left: 0;
  });
}

.info-driver__driver-name {
  display: block;
  font-size: 14px;

  .lg-block({
    text-align: center;
    font-size: 12px;
  });
}

.info-driver__drivers-car {
  font-size: 14px;
  color: @grey;

  .lg-block({
    text-align: center;
    font-size: 12px;
  });
}

.info-driver__rating {
  display: block;
  margin: 0 auto;
  line-height: 17px;
  width: 37px;
  box-shadow: 0 0 0 1px red;

  .lg-block({
    display: none;
  });
}
