
.map-popup {
  box-sizing: border-box;
  background-color: transparent;
  width: 91.4%;
  max-width: 520px;
  margin-left: 4.3%;
  top: 64px;
  height: 748px;
  min-width: 343px;
  .popup__close {
      position: absolute;
      display: block;
      top: -38.4px;
      right: 0;
    }
}

.map-popup__map-api {
  overflow: hidden;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  height: 600px;
  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}


.map-popup__footer {
  border-bottom-right-radius: 4px;
  border-bottom-left-radius: 4px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  height: 148px;
  background-color: #ffffff;
  padding: 16px 50px 32px 50px;

  & > div:first-child {
    display: table;
    width: 100%;

    margin-bottom: 16px;
  }
  .map-popup__footer-head {
    display: table-row;
    span {
      display: table-cell;
      margin-right: 54px;
      font-size: 12px;
      color: @grey;
    }
  }
  .map-popup__footer-text {
    display: table-row;
    span {
      display: table-cell;
      font-size: 14px;
      color: @lightDark;
    }
  }

  button {
    display: block;
    height: 48px;
    flex-shrink: 0;

    padding: 0;

    span {
      // line-height: 48px;
    }
  }
}
