
.tag-status {
  display: flex;
  font-size: 11px;
  line-height: 1em;
  font-weight: 500;
  text-transform: uppercase;
  color: #ffffff;
  align-items: center;
  justify-content: center;
  padding: 5px 8px;
  height: 24px;
  width: 80px;
  box-sizing: border-box;
  border-radius: 4px;
  background-color: @dark;

  &--new {
    background-color: @green;
  }

  &--expires {
    background-color: @yellow;
  }
  &--alert {
    background-color: @red;
  }
}
