.auto-info-lk375 {
  box-sizing: border-box;
  display: block;
  margin: 0 auto;
  width: 100%;
  max-width: 530px;
  align-self: center;
  background-color: #ffffff;
  padding: 0 16px 32px 16px;
  border-radius: 4px;
  border: 1px solid @lightGrey;

  .auto-info__slider-wrapper {
    border: none;
    border-bottom: 1px solid @lightGrey;
    padding-left: 0;
    padding-right: 0;
    padding-top: 0;
    padding-bottom: 32px;
    width: 100%;
  }

  .auto-slider__big {
    margin-left: -16px;
    margin-right: -16px;
  }
  .slider-1__main {
    margin-left: -16px;
    margin-right: -16px;
  }

  .auto-info-lk375__price-block {
    height: 70px;
    border-bottom: 1px solid @lightGrey;
    display: flex;
    align-items: center;
    justify-content: space-between;

    & > span {
      font-size: 18px;
      color: @dark;
      font-weight: 500;
      span {
        font-size: 14px;
        color: @grey;
        font-weight: 400;
        padding-right: 18px;
      }
    }
  }


  .auto-info-lk375__top-buttons {
    height: 110px;
    border-bottom: 1px solid @lightGrey;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
    margin-bottom: 24px;
  }

  .route-info__addit-places-left {
    display: flex;
    align-items: flex-end;
    color: @grey;
    font-size: 14px;
  }

  .route-info__places-icons {
    margin-left: 8px;
    display: flex;
    li {
      width: 24px;
      height: 24px;
      flex-shrink: 0;
    }
    li.green svg path {
      fill: @green;
    }
  }

  .route-info__addit-info {
    padding-bottom: 24px;
    border-bottom: 1px solid @lightGrey;
  }

  .direct-cart__info {
    padding: 29px 0 24px 0;
    border: none;
    border-bottom: 1px solid @lightGrey;
    border-radius: 0;
  }

  .driver-info__inner {
    & > div:first-child {
      display: flex;
    }
  }
}

.auto-info-lk375__info {
  padding: 0 0 25px 0;

  .route-info__button-map {
    margin-top: 60px;
    color: @grey;
    border-bottom-color: @grey;
  }

  .route-info__city span:last-child {
    width: 237px;
    bottom: auto;
    top: 110%;
  }

}

.auto-info-lk375__title {
  margin-bottom: 32px;

  .lg-block({
    font-size: 20px;
  });
}

.auto-info-lk375__driver-info {
  padding: 29px 0 24px 0;
  border-bottom: 1px solid @lightGrey;
  .wrapper-block {
    display: flex;
    align-items: flex-start;
  }
}

.auto-info-lk375__driver-avatar {
  margin-right: 16px;
}

.auto-info-lk375__driver-name {
  font-size: 16px;
  font-weight: 500;
  margin-bottom: 11px;

  & + .button {
    margin-bottom: 24px;
  }
}


.auto-info-lk375__car-brand,
.auto-info-lk375__car-color {
  font-size: 16px;
}

.auto-info-lk375__car-color {
  margin-bottom: 16px;
}

.auto-info-lk375__driver-icons {
  display: flex;
}

.auto-info-lk375__footer {
  padding-top: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;



  .button:first-child {
    // box-sizing: border-box;
    display: block;
    width: 192px;
    margin-bottom: 15px;
  }
  .auto-info-lk375__conditions {
    width: auto;
  }
}
