
.search-result {
  // box-sizing: border-box;
  width: 100%;
}

.search-result__inner {
  display: flex;
  align-items: flex-start;

  .lg-block({
    position: relative;
  });
}

.search-result__title {
  width: 100%;
  margin-bottom: 32px;
}

.search-result__filter {
  .lg-block({
    position: absolute;
    // top: 48px;
    display: none;
  });
}

.search-result__content {
  width: 735px;
}

.search-result__content-head {
  display: flex;
  align-items: flex-end;
  margin-bottom: 16px;

  .md-block({
    flex-direction: column;
    align-items: stretch;
  });
}

.search-result__filter-open {
  width: 256px;
  display: none;

  .lg-block({
    display: flex;
  });

  .md-block({
    width: 100%;
    margin-bottom: 24px;
  });
}

.search-result__sorting {
  margin-left: auto;

  .field--drop-block > div {
    display: flex;
    align-items: center;
  }

}

.search-result__content {
  margin-left: 32px;
  flex-basis: 500px;
  flex-grow: 1;

  .lg-block({
    margin-left: 0;
  });
}

.search-result__card {
  // background-color: #ffffff;
  // height: 160px;
}

.search-result__card:not(:last-of-type) {
  margin-bottom: 16px;

  .md-block({
    margin-bottom: 24px;
  });
}

.show {
  display: block;
}
