.transfer-card {
  box-sizing: border-box;
  display: block;
  margin: 0 auto;
  width: 100%;
  max-width: 530px;
  align-self: center;
  background-color: #ffffff;
  padding: 24px 16px 32px 16px;
  border-radius: 4px;
  border: 1px solid @lightGrey;


  .route-info__addit-places-left {
    display: flex;
    align-items: flex-end;
    color: @grey;
    font-size: 14px;
  }

  .route-info__places-icons {
    margin-left: 8px;
    display: flex;
    li {
      width: 24px;
      height: 24px;
      flex-shrink: 0;
    }
    li.green svg path {
      fill: @green;
    }
  }

  .route-info__addit-info {
    padding-bottom: 24px;
    border-bottom: 1px solid @lightGrey;
  }

  .direct-cart__info {
    padding: 29px 0 24px 0;
    border: none;
    border-bottom: 1px solid @lightGrey;
    border-radius: 0;
  }

  .driver-info__inner {
    & > div:first-child {
      display: flex;
    }
  }
}

.transfer-card__info {
  padding: 0 0 31px 0;

  .route-info__button-map {
    margin-top: 60px;
    color: @grey;
    border-bottom-color: @grey;
  }

  .route-info__city span:last-child {
    width: 237px;
    bottom: auto;
    top: 110%;
  }

}

.transfer-card__title {
  margin-bottom: 32px;

  .lg-block({
    font-size: 20px;
  });
}

.transfer-card__driver-info {
  padding: 29px 0 24px 0;
  border-bottom: 1px solid @lightGrey;
  .wrapper-block {
    display: flex;
    align-items: flex-start;
  }
}

.transfer-card__driver-avatar {
  margin-right: 16px;
}

.transfer-card__driver-name {
  font-size: 16px;
  font-weight: 500;
  margin-bottom: 11px;

  & + .button {
    margin-bottom: 24px;
  }
}


.transfer-card__car-brand,
.transfer-card__car-color {
  font-size: 16px;
}

.transfer-card__car-color {
  margin-bottom: 16px;
}

.transfer-card__driver-icons {
  display: flex;
}

.transfer-card__footer {
  padding-top: 24px;

  .button {
    display: block;
    width: 192px;
    margin: 0 auto;
  }
}

.transfer-card__auto-short {
  padding: 24px 0;
}
