.driver-auto-edit {
  width: 100%;
  max-width: 768px;
  border-radius: 4px;

}

.driver-auto-edit__container {
  box-sizing: border-box;
  position: relative;
  border-radius: 4px;
  background-color: #ffffff;
  padding: 24px 16px 48px 16px;
}

.driver-auto-edit__title {
  margin-bottom: 32px;
}

.driver-auto-edit__wrapper-data {
  display: flex;
  flex-direction: column;

}

.driver-auto-edit__table {
  display: table;
}

.driver-auto-edit__table-row {
  display: table-row;

  &:first-child {
    font-size: 14px;
    color: @dark;
    font-weight: 500;
  }
  &:not(:first-child) {
    font-size: 16px;
    color: @lightDark;
    font-weight: 400;
  }
}

.driver-auto-edit__table-cell {
  position: relative;
  display: table-cell;
  line-height: 19px;
  &:not(:last-child) {
    padding-right: 32px;
  }
  &:first-child {
    padding-right: 32px;
  }

  .tag-status {
    position: absolute;
  }

}

.driver-auto-edit__places-list {
  position: absolute;
  display: flex;
}

.driver-auto-edit__image-list {
  display: flex;
  flex-wrap: wrap;
  margin: -8px;
  padding-bottom: 8px;

  @media (max-width: 560px) {
    margin: -8px 0;
    justify-content: space-between;
  }
}


.driver-auto-edit__image-item {
  position: relative;
  width: 30%;
  margin: 8px;
  &--upload {
    height: auto;
    label {
      width: 100%;
      height: 120px;
    }
  }

  @media (max-width: 560px) {
    width: 46%;
    margin: 8px 0;
  }
}

.driver-auto-edit__image-del {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  box-shadow: none;
  border: thin solid #ffffff;
  opacity: 0.3;
  top: 2px;
  right: 2px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  padding: 0;
  margin: 0;
  cursor: pointer;
  transform: rotate(45deg);
  transition-duration: 0.5s;
  &::before{
    content: "";
    position: absolute;
    left: calc(50% - 5px);
    top: calc(50% - 1px);
    display: block;
    width: 10px;
    height: 2px;
    background-color: #ffffff;
    transition-duration: 0.5s;
  }

  &::after {
    content: "";
    display: block;
    position: absolute;
    left: calc(50% - 1px);
    top: calc(50% - 5px);
    width: 2px;
    height: 10px;
    background-color: #ffffff;
    transition-duration: 0.5s;
  }

  &:hover {
    border-color: @red;
    opacity: 1;

    &::before,
    &::after {
      background-color: @red;
    }
  }
}

.driver-auto-edit__picture {
  width: 100%;
  height: 100px;

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.driver-auto-edit__image-footer {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  border: 1px solid @lightGrey;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 32px;

  font-size: 12px;
}

.driver-auto-edit__inner-data {
  .field {
    margin-bottom: 16px;
  }
}

.driver-auto-edit__text {
  margin-top: 32px;
  margin-bottom: 32px;

  textarea {
    height: 164px;
  }
}

.driver-auto-edit__footer {
  display: flex;
  flex-direction: column-reverse;

}

