.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: @white;
  padding-top: 20px;
  padding-bottom: 20px;
  min-height: 80px;
}

.header__logo {
  font-family: "Geometria", Arial, Helvetica, sans-serif;
  font-weight: 700;
  font-size: 21px;
  line-height: 1em;
  color: @green;
  & > a {
    color: inherit;
    text-decoration: none;
    color: inherit;
  }
}
