
.change-pass {
  box-sizing: border-box;
  width: 640px;
  // margin-left: 32px;
  padding: 32px 48px 48px 48px;
  border-radius: 4px;
  border: 1px solid @lightGrey;
  background-color: #ffffff;

  .field {
    min-width: 311px;
    width: 60%;
    margin-bottom: 16px;
  }

  .lg-block({
    // margin-left: 0;
  });

  .md-block({
    width: 100%;
    padding: 24px 16px 48px 16px;
  });
}

.change-pass__title {
  margin-bottom: 16px;
}

.change-pass__desc {
  color: @grey;
  font-size: 14px;
  margin-bottom: 24px;
}

.change-pass__inner {
  display: flex;
  align-items: flex-end;
  margin-bottom: 16px;

  .field {
    margin-right: 24px;
    width: 60%;
    margin-bottom: 0;
  }

  .button--lined {
    font-weight: 400;
  }

  .md-block({
    flex-direction: column-reverse;
    align-items: stretch;

    .button--lined {
      align-self: center;
      margin-bottom: 24px;
    }
  });
}

.change-pass__submit {
  margin-top: 32px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
