
.slider-1__main {
  height: 288px;
  border-radius: 4px;
  margin-bottom: 24px;
  overflow: hidden;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.slider-1__bottom {
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider-1__info-previews {
  ul {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  li {
   width: 91px;
   height: 64px;
   margin-left: 12px;
   margin-right: 12px;
   border-radius: 4px;
   overflow: hidden;

   img {
     width: 100%;
     height: 100%;
     object-fit: cover;
   }
  }
 }

 .slider-1__info-control {
  display: block;
  width: 34px;
  height: 64px;
  margin-left: 12px;
  margin-right: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  padding: 0;
  border-radius: 0;

  span {
    position: static;
    margin: 0;
    display: block;
    width: 18px;
    height: 18px;
    background-image: url("../img/icon-arrow-down.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 200%;
    pointer-events: none;
  }

  &--prev > span {
    transform: rotate(90deg);
  }
  &--next > span {
    transform: rotate(-90deg);
  }
 }
