.booking-auto-card {
  display: block;
  height: 173px;
  border-radius: 4px;
  background-color: #ffffff;
  border: 1px solid @lightGrey;
}

.booking-auto-card__content {
  display: flex;
  height: 128px;
  border-bottom: 1px solid @lightGrey;

  .md-block({
    flex-direction: column;
  });
}

.booking-auto-card__status {
  position: relative;
  display: inline-block;
  width: 32px;
  margin-left: auto;
  border-top-right-radius: 4px;
  background-position: center;
  background-repeat: no-repeat;

  &::after {
    content: "";
    position: absolute;
    display: block;
    width: 24px;
    height: 24px;
    left: calc(50% - 12px);
    top: calc(50% - 12px);

    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url("../img/checked-white.svg");
  }

  .md-block({
    order: -1;
    align-self: flex-end;
    height: 32px;

    &::before {
      content: "";
      display: inline;
      padding-right: 8px;
      // background-color: white;
      color: @green;
      font-size: 16px;
      line-height: 32px;
      font-weight: 400;
      position: absolute;
      top: 0;
      left: -220px;
    }
  });

  &--confirmed {
    background-color: @green;
      .md-block({
        &::before {
        content: "Водитель подтвердил бронь";
      }
    });


  }
  &--canceled {
    background-color: @red;
  }
  &--awaiting {
    background-color: @yellow;
  }


}

.booking-auto-card__driver {
  .md-block({
    display: none;
  });
}

.booking-auto-card__info {
  .md-block({
    display: none;
  });
}

.booking-auto-card__places-list {
  display: flex;

  // .md-block({
  //   display: none;
  // });
}


.booking-auto-card__links {
  display: flex;
  align-items: center;

  .md-block({
    display: none;
  });
}

.booking-auto-card__more {
  display: none;
  .md-block({
    display: block;
  });
}
