
.auto-info__inner {
  box-sizing: border-box;
  padding: 32px;
  // display: flex;
  border-radius: 4px;
  border: 1px solid @lightGrey;
  background-color: #ffffff;
  // height: 722px;

  .lg-block({
    padding: 32px 48px;
    border-radius: 0;
    border: none;
    background-color: transparent;
  });

  .md-block({
    padding: 0;
  })
}

.auto-info__inner-container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  height: 550px;
  padding-bottom: 31px;
  margin-bottom: 24px;
  border-bottom: 1px solid @lightGrey;

  .lg-block({
    flex-wrap: nowrap;
    height: auto;
    padding: 0;
    margin: 0;
    border-bottom: none;
  });
}

.auto-info__slider-wrapper {
  box-sizing: border-box;
  flex-basis: 100%;
  width: 39.9%;
  min-width: 447px;
  order: -1;
  margin-right: 48px;

  .lg-block({
    order: 1;
    width: 100%;
    // max-width: 520px;
    margin-right: 0;
    padding-top: 32px;
    padding-left: 92px;
    padding-right: 92px;
    padding-bottom: 24px;
    align-self: center;
    background-color: #ffffff;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
    border: 1px solid @lightGrey;
    border-bottom: none;
  });

  .md-block({
    padding: 0;
    padding-bottom: 32px;
    min-width: 290px;

    .slider-1__main {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
      height: 205px;
    }

    .slider-1__bottom {
      margin-left: 15px;
      margin-right: 15px;
      padding-bottom: 32px;
      // border-bottom: 1px solid @lightGrey;
    }

    .slider-1__info-previews {
      li:last-child {
        display: none;
      }
    }
  });
}

.auto-info__title-wrapper {
  width: 47,2%;
  margin-bottom: 21px;

  .lg-block({
    width: 100%;
  });

  .md-block({
    margin-bottom: 34px;
  });
}

.auto-info__title {
  font-size: 24px;

  .lg-block({
    font-size: 32px;
    margin-bottom: 4px;
  });

  .md-block({
    box-sizing: border-box;
    font-size: 28px;
    width: 100%;
    padding-right: 10px;
    margin-bottom: 8px;
  });

}

.auto-info__content-wrapper {
  width: 47,2%;

  .lg-block({
    box-sizing: border-box;
    width: 100%;
    padding-left: 48px;
    padding-right: 48px;
    border-left: 1px solid @lightGrey;
    border-right: 1px solid @lightGrey;
    background-color: #ffffff;
    order: 3;
  });

  .md-block({
    padding-left: 16px;
    padding-right: 16px;
  });

}

.auto-info__auto-table {
  padding-bottom: 24px;
  margin-bottom: 24px;
  border-bottom: 1px solid @lightGrey;

  // .lg-block({border-bottom: none;});
}

.auto-info__additional {
  padding-bottom: 26px;
  margin-bottom: 32px;
  border-bottom: 1px solid @lightGrey;

  // .lg-block({border-bottom: none;});

}

.auto-info__price-wrapper {
  width: 47,2%;
  display: flex;
  // justify-content: space-between;
  // align-items: center;
  flex-direction: column;

  .lg-block({
    box-sizing: border-box;
    padding-left: 48px;
    padding-right: 48px;
    width: 100%;
    background-color: #ffffff;
    border-left: 1px solid @lightGrey;
    border-right: 1px solid @lightGrey;
    order: 2
  });

  .md-block({
    padding: 0;
    padding-left: 15px;
    padding-right: 15px;
  });
}

.auto-info__price-inner {
  margin-bottom: 15px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  & > .auto-info__price-mobile {
    display: none;
  }

  .lg-block({
    padding-top: 32px;
    border-top: 1px solid @lightGrey;
  });

  .md-block({
    display: flex;
    flex-direction: column;

    & > a {
      display: none;
    }
    & > .auto-info__price {
      display: none;
    }
    & > .auto-info__price-mobile {
      width: 100%;
      padding-top: 21px;
      padding-bottom: 23px;
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;

      .btn-price__price {
        // margin-right: 30px;
        margin-bottom: 0;
      }
    }

  });

}


.auto-info__in-stock {
  color: @green;
  display: flex;
  align-items: center;
  &::before {
    content: "";
    display: block;
    width: 16px;
    height: 16px;
    background-image: url("../img/checked.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    margin-right: 8px;
  }

  .lg-block({
    font-size: 14px;
  })

}

.auto-info__price {
  margin-left: auto;
  margin-right: 24px;
  align-self: center;
}
.auto-info__button {
  width: 192px;
  box-sizing: border-box;
  align-self: center;
}

.auto-info__more {
  align-self: flex-end;
  font-size: 14px;
  color: @grey;
  border-color: @grey;

  .lg-block({
    margin-bottom: 32px;
  });

  .md-block({
    align-self: center;
  });

}

.auto-info__price-border {
  .md-block({
    border-bottom: 1px solid @lightGrey;
  });
}



.auto-info__footer {
  // width: 1%;
  padding-left: 288px;
  padding-right: 192px;
  margin-left: -32px;
  margin-right: -32px;

  & > a {
    display: none;
  }

  .lg-block({
    margin: 0;
    padding: 0 48px;
    padding-bottom: 32px;
    background-color: #ffffff;
    border-bottom-right-radius: 4px;
    border-bottom-left-radius: 4px;
    border-left: 1px solid @lightGrey;
    border-right: 1px solid @lightGrey;
    border-bottom: 1px solid @lightGrey;
  });

  .md-block({
    padding: 0 16px;
    display: flex;
    flex-direction: column;

    & > a {
      display: block;
      align-self: center;
      margin-top: 32px;
      margin-bottom: 32px;
    }
  });

}

.auto-info__sybtitle {
  font-size: 20px;
  margin-bottom: 11px;
}

.auto-info__text {
  font-size: 14px;
}


#auto-stats-table-1 {
  .md-block({
    display: none;
  });
}

#auto-stats-table-2 {
  display: none;
  .md-block({
    display: table;
  });
}
