.profile-notification {
  box-sizing: border-box;
  width: 640px;
  // margin-left: 32px;
  padding: 32px 48px 48px 48px;
  border-radius: 4px;
  border: 1px solid @lightGrey;
  background-color: #ffffff;


  .lg-block({
    // margin-left: 0;
  });

  .md-block({
    width: 100%;
    padding: 24px 16px 48px 16px;
  });
}

.profile-notification__title {
  margin-bottom: 16px;
}

.profile-notification__desc {
  color: @grey;
  font-size: 14px;
  margin-bottom: 24px;
}

.profile-notification__inner {
  margin-bottom: 32px;

  .profile-notification__title {
    margin-bottom: 8px;
  }


}


.profile-notification__item {
  width: 100%;
  max-width: 450px;

  &:not(:last-child) {
    margin-bottom: 12px;
  }

}

.profile-notification__check {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;

  & > span:first-child {
    margin-right: 32px;
  }

  .custom-check {
    border-radius: 50%;
  }
}

.profile-notification__submit {
  display: block;
  margin: 48px auto 0 auto;
}
