.airport-back-block {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 23px 21px;
  width: 286.7px;
  height: 81.49px;
  border-radius: 4px;
  border: 1px solid #E8EBF0;
  background-color: #ffffff;
}

.airport-back-block__left {
  & > li {
    height: 14px;
  }
}

.airport-back-block__from,
.airport-back-block__to {

  font-size: 14px;
  line-height: 14px;

  display: flex;
  align-items: center;

  & > span:first-child {
    display: block;
    width: 5.5px;
    height: 5.5px;
    border-radius: 50%;
    margin-right: 12.88px;
  }
}

.airport-back-block__from {
  & > span {
    background-color: #FFAD0D;
  }
}
.airport-back-block__to {
  & > span {
    background-color: #0BB07B;
  }
}

.airport-back-block__sep {
  display: block;
  background-color: #E1E4E8;
  width: 1px;
  height: 100%;
  margin-left: 2px;
}

.airport-back-block__price {
  display: flex;
  width: 58px;
  height: 24px;
  align-items: center;
  justify-content: center;
  border-radius: 12px;
  border: 1px solid #E8EBF0;
  & > span {
    font-size: 14px;
    color: #B0B0B0;
    font-weight: 500px;
  }
}
