
.auto-cart__wrapper {
  .md-block({
    display: flex;
    flex-direction: column;
  });
}

.auto-cart__title {
  margin-bottom: 32px;

  .md-block({
    font-size: 28px;
  });
}

.auto-cart__alarm {
  margin-bottom: 16px;

  .lg-block({
    display: none;
  });

  .md-block({
    order: 2;
    display: block;
    margin-top: 24px;
    padding: 16px;

    .alarma__icon {
      display: none;
    }

    .alarma__message {
      font-size: 14px;
      font-weight: 400;
      margin: 0;
      margin-bottom: 8px;
      padding: 0;
    }

    .alarma__description {
      font-size: 14px;
      font-weight: 400;
      padding: 0;
      border: none;
      margin: 0;
      color: @grey;
    }
  });
}

.auto-cart__form {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  .md-block({
    flex-direction: column;
    align-items: stretch;
    justify-content: flex-start;
  });
}

.auto-cart__main-info {
  box-sizing: border-box;
  width: 48.57%;
  padding: 32px 64px;
  background-color: #ffffff;
  border-radius: 4px;
  border: 1px solid @lightGrey;

  fieldset {
    width: 100%;
    margin: 0;
    margin-bottom: 32px;
    // background-color: green;

    legend {
      font-family: "Geometria", Arial, Helvetica, sans-serif;
      color: @dark;
      font-size: 20px;
      line-height: 1em;
      margin-bottom: 16px;
    }

    .field:not(:last-child) {
      margin-bottom: 8px;
    }


  }

  .lg-block({
      padding: 32px;
    });

  .md-block({
    width: 100%;
    order: 2;
  });
}

.auto-cart__form-footer {
  display: flex;
  align-items: flex-start;
  // flex-wrap: wrap;
  justify-content: flex-end;
}

.auto-cart__button-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 24px;
}

.auto-cart__submit {
  margin-bottom: 15px;
}

.auto-cart__reset {
  color: @red;
  border-bottom-color: @red;
}

.auto-cart__auto-info {
  box-sizing: border-box;
  width: 48.57%;
  padding: 48px;
  padding-bottom: 40px;
  background-color: #ffffff;
  border-radius: 4px;
  border: 1px solid @lightGrey;

  .lg-block({
    padding: 32px 32px 24px 32px;
  });

  .md-block({
    width: 100%;
    margin-bottom: 24px;
  });
}
