.popup-mask,
.popup__close {
  cursor: pointer;
}


.popup-mask {
	position: fixed;
	z-index: 9000;
	background: rgba(0,0,0,0.75);
	display: none;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
}

.popup {
	display: none;
	position: absolute;
	z-index: 9500;
	left: 0;
  right: 0;
	margin: 0 auto;
	background: #E8EBF0;
}


.popup__close {
	display: block;
  position: absolute;

	overflow: hidden;
	width: 25px;
	height: 25px;
}

.popup__close::before,
.popup__close::after {
  content: " ";
  display: block;
	position: absolute;
	width: 25px;
	height: 2px;
	background: #B0B0B0;
	margin: 12px 0 0 0;
}

.popup__close:hover::before,
.popup__close:hover::after {
  background: #cccccc;
}

.popup__close::before {
			transform: rotate(45deg);
}

.popup__close::after {
			transform: rotate(-45deg);
}
