.document-status {
  padding: 16px 24px 16px 48px;
  border-radius: 4px;
  border: 1px solid @lightGrey;
  background-color: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 16px;

  .md-block({
    padding: 0;
    border: none;
    background-color: transparent;
    flex-direction: column-reverse;
    align-items: stretch;
  });
}

.document-status__inner:last-child {
  .md-block({
    order: 2;
    margin-bottom: 8px;
  });
}

.document-status__inner:first-child {

  .md-block({
    padding: 16px;
    background-color: #ffffff;
    border-radius: 4px;
    border: 1px solid @lightGrey;
  });

}

.document-status__title {
  margin-bottom: 8px;
  display: flex;
  align-items: center;

  &::after {
    content: "";
    display: inline-block;
    margin-left: 10px;
    height: 16px;
    width: 16px;
    background-image: url("../img/icon-what-is.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.documents-status__list {
  font-size: 12px;
  line-height: 14px;
}

.document-status__item {
  margin-bottom: 8px;

  &::before {
    content: "";
    display: inline-block;
    width: 12px;
    height: 12px;
    margin-top: 1px;
    margin-right: 4px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }

  &--checked::before {
    background-image: url("../img/checkbox-1.svg");
  }

  &--expects::before {
    background-image: url("../img/frame-4.svg");
  }

  &--rejected::before {
    background-image: url("../img/checkbox-alert.svg");
  }
}

.document-status__status-field {
  border-radius: 4px;
  border: 1px solid @green;
  padding: 12px 8px;
  box-sizing: border-box;
  margin-top: 8px;
  width: 226px;

  p {
    text-align: center;
    font-size: 14px;
  }

  p:first-child {
    font-weight: 500;
    color: @green;
    margin-bottom: 8px;
  }

  p:last-child {
    color: @grey;
  }

  .md-block({
    width: 100%;

    p:last-child {
      color: @lightDark;
    }
  });
}
