.block-1 {

  padding-top: 80px;
  padding-bottom: 80px;
  display: flex;
  align-items: center;
  position: relative;

  .lg-block({
    padding-bottom: 64px;
  });

  .md-block({
    flex-direction: column-reverse;
    // width: 375px;
    margin: 0 auto;
  });
}
.block-1__background {
  box-sizing: border-box;
  width: 50%;
  overflow: hidden;

  & > ul {
    display: flex;

    & > li:last-child {
      opacity: 0.5;
    }
  }

  & > ul:not(:last-child) {
    margin-bottom: 14px;
  }

  & > ul > li:not(:last-child) {
    margin-right: 14px;
  }

  & > ul:first-child,
  & > ul:last-child {
    & > li:first-child {
      margin-left: 14px;
    }
  }

  .lg-block({
    margin-left: 32px;
    width: 40%;
  });

  .md-block({
    width: 100%;
    margin-left: 0;
    padding-left: 16px;
    margin-bottom: 24px;
  });
}

.block-1__wrapper {
  width: 50%;
  & > div {
    width: 100%;

    .md-block({
      display: flex;
      flex-direction: column;
    });
  }

  .lg-block({
    width: 56%;
    margin-right: auto;
  });

  .md-block({
    width: 100%;
  });


}

.block-1__title {
  margin-bottom: 24px;
  font-size: 32px;
  line-height: 1em;
  color: @dark;

  .lg-block({
    font-size: 24px;
  });
}

.block-1__text {
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 400;
  color: @lightDark;

  .lg-block({
    // line-height: 1.35em;
  });

  .md-block({
    margin-bottom: 24px;
  });
}

.block-1__button {
  .md-block({
    align-self: center;
    margin-left: auto;
    margin-right: auto;
  });
}
