.block-3__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding-bottom: 80px;

  .lg-block({
    padding-bottom: 48px;
  });

  .md-block({
    flex-direction: column;
    flex-wrap: nowrap;
    padding-left: 0;
    padding-right: 0;
  });
}

.block-3__title {
  width: 100%;
  margin-bottom: 40px;
  font-size: 32px;
  color: @dark;
  text-align: center;

}

.block-3__subblock {
  width: 48.5%;
  box-sizing: border-box;
  padding-bottom: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: @white;
  border: 1px solid @lightGrey;
  border-radius: 4px;

  .lg-block({
    width: 47.7%;
  });

  .md-block({
    width: 100%;
    border: none;
    border-radius: 0;
    background-color: transparent;
    padding-bottom: 0;

    &:not(:last-child) {
      & .block-3__button {
        display: none;
      }
    }
  });
}

.block-3__image {
  align-self: stretch;
  margin-bottom: 26px;

  img {
    width: 100%;
    height: auto;
  }
  .md-block({
    height: 140px;
    overflow: hidden;
  });
}

.block-3__subtitle {
  align-self: flex-start;
  margin-bottom: 12px;
  padding: 0 24px 0 32px;
  font-size: 24px;
  font-weight: 500;
  color: @dark;

  .lg-block({
    font-size: 20px;
    padding: 0 24px;
  });
}

.block-3__text {
  margin-bottom: 32px;
  padding: 0 24px 0 32px;
  font-size: 14px;
  font-weight: 400;
  color: @lightDark;

  .lg-block({
    padding: 0 24px;
  });
}

.block-3__button {
  margin-top: auto;
}
