
.short-info__title {
  margin-bottom: 25px;
}

.short-info__inner {
  display: flex;
  // align-items: flex-start;
  // height: 144px;

  .lg-block({
    flex-direction: column;
  });

  .md-block({
    flex-direction: row;
  });
}

.short-info__photo {
  width: 192px;
  height: 144px;
  overflow: hidden;
  border-radius: 4px;
  margin-right: 32px;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .lg-block({
    margin-right: 0;
    width: 100%;
    height: 144px;
    margin-bottom: 24px;
  });

  .md-block({
    width: 64px;
    height: 64px;
    margin-right: 24px;
  });
}

.short-info__col-right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.short-info__subtitle {
  margin-bottom: auto;
  h4 {
    font-size: 14px;
    font-family: "Roboto", Arial, Helvetica, sans-serif;
    font-weight: 500;
    margin-bottom: 8px;

  }

  .lg-block({
    margin-bottom: 24px;
  });
}

.short-info__table {
  margin: 0;
  th {
    padding-top: 0;
  }
  td {
    line-height: 1em;
  }
 }

 #stats-auto-table-1 {
  .lg-block({display: none;});
 }

 #stats-auto-table-2 {
  display: none;
  .lg-block({
    display: table;
  margin-right: -25px;});
  .md-block({
    display: none;
  });
 }

 #stats-auto-table-3 {
   display: none;

   tr:nth-child(3) > th {
     padding-top: 16px;
   }

   .md-block({
     display: table;
   });
 }
