
.lk-add-transf {
  width: 100%;
  padding: 48px;
  border-radius: 4px;
  background-color: #ffffff;
  border: 1px solid @lightGrey;

  .lg-block({
    padding: 32px 24px 48px 24px;
  });

  .md-block({
    padding: 24px 16px 32px 16px;
  });
}

.lk-add-transf__form {
  box-sizing: border-box;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;

  .md-block({
    display: block;
  });
}

.lk-add-transf__map {

  .map__footer {
    display: none;
    .lg-block({
      display: block;
    });
  }

  .md-block({
    display: none;
  });
}

.lk-add-transf__left-col {
  width: 35.93%;
  min-width: 296px;

  .lg-block({
    width: 50%;
  });

  .md-block({
    width: 100%;
  });
}

.lk-add-transf__field {
  .range-v1 {
    display: flex;
    align-items: flex-end;
    padding-bottom: 32px;

    .range__price-values {
      margin-top: 0;
      margin-bottom: 8px;
      margin-left: 16px;
    }

    .range__value-line {
      left: 0;
    }

    .range__pin {
      left: auto;
      right: calc(45% - 10px);
    }
  }
}

.lk-add-transf__field:nth-child(1) {
  margin-bottom: 16px;
}

.lk-add-transf__field:nth-child(2) {
  display: flex;
  flex-direction: column;

  .field:nth-of-type(1) {
    margin-bottom: 16px;
  }
}

.lk-add-transf__subtitle {
  margin-bottom: 16px;
}

.lk-add-transf__view-map {
  display: inline-block;
  align-self: center;
  margin-top: 15px;
  margin-bottom: 32px;

  &--range {
    margin-bottom: 0;
  }

  .filter-search__range {
  height: 30px;
  display: flex;
  align-items: flex-end;
  padding-bottom: 32px;
  margin-bottom: 32px;
  border-bottom: 1px solid @lightGrey;

  .range__value-line {
    left: 0;
  }

  .range__pin {
    left: calc(55% - 10px);
  }

  .range__price-values {
    margin-left: 16px;
    margin-bottom: 8px;
  }
}
}



.lk-add-transf__date-time {
  display: flex;
  margin-bottom: 24px;

  & > div:last-child {
    box-sizing: border-box;
    width: 128px;
    margin-left: 24px;
  }

  & > div:first-child {
    flex-grow: 1;
  }

  .lg-block({
    display: block;
    & > div:first-child {
      margin-bottom: 8px;
    }

    & > div:last-child {
      width: 100%;
      margin-left: 0;
    }
  });
}

.lk-add-transf__right-col {
  width: 60.9%;
  // flex-basis: 55.7%;

  p {
    font-size: 14px;
    color: @grey;
  }

  & > h3 {
    display: none;
  }

  .lg-block({
    width: 45.9%;
    min-width: 272px;

    & > h3 {
      display: block;
    }
  });

  .md-block({
    width: 100%;

    & > h3 {
      display: none;
    }
  });
}

.lk-add-transf__map-wrapper {
  height: 486px;
  overflow: hidden;
  margin-bottom: 32px;
  position: relative;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .map-radius__pin {
    position: absolute;
    display: block;
    width: 280px;
    height: 280px;
    left: calc(50% - 140px);
    top: 5px;
    border-radius: 50%;
    border: 2px solid @lightGreen;
    background-image: url("../img/iconfinder_pin_293694.svg");
    background-position: center;
    background-repeat: no-repeat;

    .lg-block({
      width: 192px;
      height: 192px;
      top: 23px;
      left: calc(50% - 96px);
    });
  }

  .lg-block({
    height: 331px;
    margin-bottom: 0;
  });
}

.lk-add-transf__map-legend {
  display: none;
  position: relative;
  height: 48px;
  margin-top: -48px;
  margin-bottom: 16px;
  z-index: 2;
  background-color: #F8FAFA;
  align-items: center;
  justify-content: center;

  & > div {
    display: table;
  }

  p {
    display: table-row;

    span {
      display: table-cell;

      &:not(:last-child) {
        padding-right: 24px;
      }

    }

    &:first-child {
      font-size: 12px;
      font-family: "Roboto Condensed", Arial, Helvetica, sans-serif;
    }
    &:nth-child(2) {
      font-size: 12px;
      color: @lightDark;
    }
  }



  .lg-block({
    display: flex;
  });
}

.lk-add-transf__footer {
  display: flex;
  align-items: center;

  p {
    font-size: 14px;
    color: @dark;
    padding-right: 31px;
  }

  .price-v1 {
    margin-right: 39px;
  }

  .lg-block({
    display: block;
    text-align: right;

    p {
      text-align: right;
      margin-bottom: 6px;
    }

    .price-v1 {
      margin-bottom: 16px;
      margin-right: 44px;
    }
  });

  .md-block({
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    p {
      text-align: left;
      margin-bottom: 0;
      padding-right: 0;
    }

    .price-v1 {
      margin-right: 0;
      margin-bottom: 0;
      margin-left: auto;
    }
  });
}

.lk-add-transf__text {
  width: 368px;

  .lg-block({
    width: 100%;
    margin-bottom: 16px;
  });

  .md-block({
    padding-top: 24px;
    border-top: 1px solid @lightGrey;
  });
}

.lk-add-transf__submit {
  .lg-block({
    margin-right: 26px;
  });

  .md-block({
    width: 100%;
    margin-right: 0;
    margin-top: 24px;
  });
}

.lk-add-transf__footer-buttons {
  width: 100%;
  display: flex;
  flex-direction: column;
  width: 100%;
}


