.lk-add-transf--edit {
  .lk-add-transf__form {
    flex-wrap: wrap;
  }

  .lk-add-transf__field:nth-child(2) {
    margin-bottom: 32px;
    border-bottom: 1px solid @lightGrey;
  }

  .lk-add-transf__field:last-child .lk-add-transf__date-time {
    margin-bottom: 0;
  }

  .lk-add-transf__map-legend {
    display: none;

    .lg-block({
      display: flex;
    });
  }

  .lk-add-transf__text {
    display: none;

    .lg-block({
      display: block;
    });
  }

  .lk-add-transf__footer {
    display: none;
    p {
      color: @grey;

      .md-block({
        color: @lightDark;
      });
    }
    .price-v1__value {
      color: @grey;

      .md-block({
        color: @lightDark;
      });
    }

    .lg-block({
      display: block;
    });

    .md-block({
      display: flex;
    });
  }
  .lk-add-transf__footer-buttons {
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-start;

    .lg-block({
      padding-top: 32px;
      flex-direction: column;
      justify-content: stretch;
      width: 100%;
    });
  }

  .lk-add-transf__submit {
    box-sizing: border-box;
    display: block;
    width: 224px;
    margin-right: 96px;
    margin-left: 32px;

    .lg-block({
      width: auto;
      margin: 0;
    });
  }

  .lk-add-transf__reset {
    display: block;
    width: 224px;
    height: 48px;
    box-sizing: border-box;
    padding: 0;

    .lg-block({
      width: auto;
      margin-top: 24px;
    });
  }
}

.lk-add-transf__field--range {
  padding-bottom: 32px;
  margin-bottom: 32px;
  border-bottom: 1px solid @lightGrey;

  .lk-add-transf__subtitle {
    margin-bottom: 0;
  }
}

.lk-add-transf__range {
  display: flex;
  width: 100%;

  .range {
    width: 50%;
    flex-grow: 1;
    align-items: flex-start;
    margin-right: 16px;
    margin-top: 28px;
  }

  .range__price-values {
    margin: 0;
  }
}

