.lk-ds {
  width: 100%;
  height: 1000px;

  &--1-1 {
    height: 905px;
  }

  &--2-1 {
    height: 960px;
  }

  .lg-block({
    height: auto;
    max-height: 1500px;
  });
}

.lk-ds__form {
  box-sizing: border-box;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  flex-wrap: wrap;

  .lg-block({
    display: block;
    padding: 32px 32px 48px 32px;
    border: 1px solid @lightGrey;
    border-radius: 4px;
    background-color: #ffffff;
  });
}

.lk-ds__map-wrapper {
  position: relative;
  box-sizing: border-box;
  order: 2;
  width: 48.57%;
  margin-right: 0;
  margin-left: auto;
  background-color: #ffffff;
  border: 1px solid @lightGrey;
  border-radius: 4px;

  .route-info__to-from {
    height: 80px;
  }

  .route-info__icon-direct {
    height: 80px;
  }

  .lk-ds__map {
    position: relative;
    height: 289px;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    .lg-block({
      height: 240px;
    });
  }

  .lk-ds__route-info {
    padding-left: 32px;
    padding-top: 32px;
    padding-right: 53px;

    .lg-block({
      padding-top: 24px;
      padding-left: 24px;
    });
  }

  .route-info::after {
    margin-top: 50px;
    background-color: transparent;
  }

  .lg-block({
    width: 100%;
    margin-bottom: 24px;

    .route-info__time-date {
      display: none;
    }
  });

  .md-block({
    display: none;
  });
}

.lk-ds__map-pin {
  display: block;
  width: 32px;
  height: 32px;
  position: absolute;
  background-position: center;
  background-repeat: no-repeat;
  background-image: url("../img/iconfinder_pin_green.svg");
  &#map-pin__1 {
    top: 16px;
    left: 146px;
  }
  &#map-pin__2 {
    bottom: 7px;
    right: 131px;
  }
}

.lk-ds__map-footer {
  padding: 0 53px 32px 57px;
  display: flex;

  .table-block {
    margin-right: 32px;
  }

  .table-block__head {
    margin-bottom: 2px;
  }

  .lg-block({
    position: absolute;
    bottom: 7px;
    right: 0;
    flex-direction: column;
    height: 90px;
    justify-content: space-between;
  });
}

.lk-ds__title-wrapper {
  box-sizing: border-box;
  width: 48.57%;
  padding-left: 64px;
  padding-right: 64px;
  border-left: 1px solid @lightGrey;
  border-right: 1px solid @lightGrey;
  background-color: #ffffff;
  &:first-child {

    padding-top: 32px;
    border-top: 1px solid @lightGrey;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;

    .lg-block({
      border-top: none;
      padding-top: 0;
    });

    .md-block({
      margin-bottom: 24px;
      border-bottom: 1px solid @lightGrey;
    });

  }




  .lk-ds__view-map {
    display: none;
    .md-block({
      display: block;
      margin: 0 auto;
      margin-bottom: 23px;
    });
  }

  .search-form__destinations {
    margin-bottom: 32px;
  }

  .lg-block({
    width: 100%;
    border: none;
    padding: 0;

  });

}

.lk-ds__title-wrapper--mobile {

  padding-bottom: 24px;
  .lg-block({
    padding-bottom: 24px;
  });
  .md-block({
    padding-bottom: 24px;
    margin-bottom: 24px;
    border-bottom: 1px solid @lightGrey;
    // display: block;
  });
}

.lk-ds-main__title {
  margin-bottom: 16px;
}


.lk-ds__checks-wrapper {
  box-sizing: border-box;
  width: 48.57%;
  padding-left: 64px;
  padding-right: 64px;
  background-color: #ffffff;
  border-left: 1px solid @lightGrey;
  border-right: 1px solid @lightGrey;

  .add-passengers {
    margin-bottom: 32px;
  }

  .lg-block({
    width: 100%;
    padding: 0;
    border: none;
    display: flex;
    justify-content: space-between;
  });

  .md-block({
    display: block;
  });
}

.lk-ds__footer {
  box-sizing: border-box;
  width: 48.57%;
  padding-left: 64px;
  padding-right: 64px;
  padding-bottom: 48px;
  padding-top: 49px;
  background-color: #ffffff;
  border: 1px solid @lightGrey;
  border-top: none;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;

  .lg-block({
    width: 100%;
    border: none;
    padding-bottom: 0;
  });

  .md-block({
    padding-left: 0;
    padding-right: 0;
  });
}

.lk-ds-main__submit {
  display: block;
  width: 192px;
  margin: 0 auto;

}



