.modal {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height:100vh;
}

.modal__close {
  display: none;
  position: absolute;
  width: 20px;
  height: 20px;
  background-image: url("../img/close-icon.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.modal__overlay {

  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10;
  // display: none;
/* фон затемнения */
  background-color: rgba(0, 0, 0, 0.65);
  position: fixed; /* фиксированное поцизионирование */
  cursor: default; /* тип курсара */
}

