
.upload-photo {
  position: relative;
  width: 104px;
  height: 100px;
  box-sizing: border-box;
  border: 1px solid #E8EBF0;;
  border-radius: 3px;
  cursor: pointer;
}

.upload-photo {
  label {
    display: flex;
    height: 100%;
    width: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    color: @grey;
    cursor: pointer;
  }

  input[type=file]{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    cursor: pointer
}

}

