.lk-menu {
  position: sticky;
  top: 0;
  box-sizing: border-box;
  display: flex;
  // flex-basis: 240px;
  flex-direction: column;
  flex-shrink: 0;
  width: 240px;
  height: 100vh;
  padding: 24px;
  padding-bottom: 40px;
  background-color: #323232;
  background-image: url("../img/lk-menu-image.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

}

.lk-menu__overlay {
  display: none;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: black;
  opacity: 0.75;
  z-index: 999;
}

.lk-menu__spacer {
  display: none;
}

.lk-menu__close-button {
  display: none;
  cursor: pointer;
}
.lk-menu__open-button {
  position: absolute;
  top: 24px;
  left: calc(50% - 12px);
  display: none;
  margin-bottom: 64px;
  width: 24px;
  text-align: center;
  height: 24px;
  cursor: pointer;
}

.lk-menu .header__logo {
  text-align: center;
  margin-bottom: 64px;
}

.lk-menu__navigation {
  display: block;
  margin-left: -24px;
  margin-right: -24px;

  ul {
    padding-right: 8px;

    // .lg-block({
    //   padding-right: 0;
    // });
  }
}

.lk-menu__top-list,
.lk-menu__second-list,
.lk-menu__footer-list {
  margin-bottom: 6px;
}

.lk-menu__underline {
  display: block;
  height: 1px;
  margin-left: 24px;
  margin-right: 24px;
  margin-bottom: 12px;
  background-color: rgba(255,255,255,0.15);

}

.lk-menu__item {
  display: block;
  height: 44px;
  display: flex;
  align-items: center;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;

  & > span:first-child {
    display: block;
    width: 4px;
    align-self: stretch;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
  }

  &--active {
    background-color: rgba(255, 255, 255, 0.15);

    & > span:first-child {
      background-color: @green;
    }

    a {
      color: #ffffff;
    }

    .lk-menu__link-ico path {
      fill: white;
    }

  }
}

.lk-menu__link-wrapper {
  padding-left: 20px;
  display: flex;
  align-items: center;
  flex-grow: 1;

}

.lk-menu__link-value {
  display: block;
  padding: 3px 4px;
  border-radius: 3px;
  margin-left: auto;
  margin-right: 8px;
  background-color: @grey;
  color: @lightDark;
  font-size: 12px;
  line-height: 12px;
  font-weight: 500;
  text-align: center;

}


.lk-menu__link {
  display: flex;
  align-items: center;
  color: #ffffff;
  font-weight: 500;
  text-transform: uppercase;
  text-decoration: none;
  font-size: 12px;
  line-height: 14px;
  font-weight: 500;
  color: @grey;

}

.lk-menu__link-ico {
  display: block;
  width: 20px;
  height: 20px;
  margin-right: 5px;
  display: flex;
  justify-content: center;
  align-items: center;

}



.lk-menu__footer {
  margin-top: auto;

  .md-block({
    margin-top: 196px;
  });
}

.lk-menu__footer-list {
  margin-left: -24px;
  margin-right: -16px;
}

.lk-menu__footer-text {
  font-size: 12px;
  color: @grey;
}

.lk-menu.lk-menu--popup {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  // display: none;
  z-index: 1000;
  transform: translate3d(-475px, 0, 0);
  transition: all 500ms ease-in-out;

  .lk-menu__inner {
    height: 90%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}


// (min-width: 768px) and

.lg-block({
  .lk-menu.lk-menu--static {
    width: 64px;
    height: 100vh;
    min-height: 768px;
    padding-top: 112px;

    .lk-menu__open-button {
      display: block;
    }

    .header__logo {
      display: none;
    }

    .lk-menu__navigation ul {
      padding-right: 0;
    }

    .lk-menu__underline {
      margin-left: 8px;
      margin-right: 8px;
    }

    .lk-menu__link-wrapper {
      flex-direction: column;
      justify-content: center;
      flex-grow: 0;
      padding-left: 12px;
      padding-right: 18px;
      width: 100%;
    }

    .lk-menu__link-value {
      margin: 0;
      align-self: stretch;
    }

    .lk-menu__link {
      flex-direction: column;
    }

    .lk-menu__link-ico {
      margin: 0;
    }

    .lk-menu__link-text {
      display: none;
    }

    .lk-menu__footer {
      display: none;
    }
  }

  .lk-menu.lk-menu--popup {
    // display: flex;
    // position: absolute;
    // z-index: 1000;


    & ~ .lk-menu__overlay {
      display: none;
    }

    // & ~ .lk-menu__spacer {
    //   display: none;
    //   width: 64px;
    // }

    .lk-menu__close-button {
      position: absolute;
      display: block;
      right: -54.4px;
      top: 66.4px;
    }

    .lk-menu__inner {
      height: 80%;
    }


    // menu active
    &.active {
      display: block;
      transform: translate3d(0, 0, 0);
      transition: all 500ms ease-in-out;
    }

    &.active ~ .lk-menu__overlay {
      transition: all 500ms ease-in-out;
      display: block;
    }
  }
});

