.md-block({

  .lk-menu.lk-menu--static {
    display: none;
  }

  .lk-menu.lk-menu--popup {
    position: absolute;
    box-sizing: border-box;
    // display: block;
    left: 0;
    top: 64px;
    width: 100%;
    min-height: calc(100vh - 64px);
    height: 748px;
    min-height: auto;
    max-height: auto;
    // max-height: 1268px;
    // min-height: 748px;
    padding-top: 16px;
    padding-bottom: 48px;
    padding-left: 0;
    padding-right: 0;
    z-index: 1000;
    transform: translate3d(-900px, 0, 0);
    transition: all 500ms ease-in-out;


    .lk-menu__close-button {
      position: absolute;
      display: none;
      right: 32px;
      top: -47px;

    }

    .lk-menu__inner {
      // height: 80%;
    }

    .lk-menu__navigation {
      margin-left: 0;
      margin-right: 0;

      ul {
        padding-right: 0;
      }
    }

    .lk-menu__item {
      height: 56px;
      & > span:first-child {
        width: 12px;
      }

      a {
        font-size: 18px;
      }
    }

    .lk-menu__link-value {
      display: none;
    }

    .lk-menu__footer-list {
      margin-left: 0;
      margin-right: 0;
    }

    .lk-menu__footer-text {
      display: none;
    }

  // menu active
    &.active {
      display: block;
      transform: translate3d(0,0,0);
      transition: all 500ms ease-in-out;
    }

    &.active ~ .lk-menu__overlay {
      transition: all 500ms ease-in-out;
      display: block;
    }
  }
});

