.page-footer {
  margin-top: 100px;

  .lg-block({
    margin-top: 80px;
  });

  // .md-block({display: none;});
}

.page-footer__wrapper {
  padding-top: 32px;
  padding-bottom: 32px;
  display: flex;
  align-items: flex-start;

  .md-block({
    flex-direction: column;
    align-items: flex-start;
    // width: 100%;
    padding-left: 0;
    padding-right: 0;
    padding-left: 64px;
  });

}

.page-footer__logo {
  flex-basis: 196px;
  a {
    display: block;
    padding-top: 8px;
    margin-bottom: 16px;
  }

  p {
    font-size: 12px;
    font-weight: 400;
    color: @lightDark;
  }

  .md-block({
    flex-basis: 100px;
  });
}

.page-footer__links {
  list-style: none;
  max-width: 700px;
  margin: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  display: grid;
  grid-template-columns: min-content min-content min-content;
  grid-column-gap: 72px;
  grid-row-gap: 15px;



  a {
    display: block;
    font-size: 14px;
    line-height: 14px;
    font-weight: 400;
    text-decoration: none;
    color: @lightDark;

    &:hover,
    &:focus {
      text-decoration: underline;
    }
  }

  .lg-block({
    grid-column-gap: 48px;
    grid-template-columns: min-content min-content;
    margin-right: auto;
  });
  .md-block({
    display: block;
    max-width: 263px;
    margin-right: 0;
    order: -1;
    margin: 0;
    margin-bottom: 20px;

    li {
      margin-bottom: 12px;
      line-height: 19px;
    }

    a {
      font-size: 16px;
    }
  });
}

