

.range__line {
  width: 100%;
  height: 4px;
  background-color: @lightGrey;
  position: relative;
  margin: 8px 0;

}

.range__value-line {
  position: absolute;
  height: 4px;
  left: 15%;
  right: 45%;
  background-color: @lightGreen;
}

.range__pin {
  display: block;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  top: -8px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: @green;
  &::after {
    content: "";
    display: block;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: #ffffff;
  }

  &--min {
    left: calc(15% - 10px);
  }
  &--max {
    right: calc(45% - 10px);
  }
}

.range__price-values {
  margin-top: 24px;

  input {
    padding-top: 7px;
    padding-bottom: 6px;
    padding-left: 14px;
    padding-right: 14px;
    text-align: right;
    height: 32px;
    width: 80px;
  }
}

.range-slide-val {
  z-index: auto !important;
}
