
.counter {
  display: flex;
  align-items: center;
}

.counter .fieldCount {
  border: none;
  display: block;
  width: 48px;
  height: 21px;
  line-height: 21px;
  text-align: center;
}

.counter .counterBut {
  display: block;
  border: none;
  background-color: transparent;
  padding: 0;
  margin: 0;
  width: 20px;
  height: 20px;
  background-position: center;
  background-repeat: no-repeat;
}

.counterBut.inc {
  background-image: url("../img/icon-plus.svg");
}

.counterBut.dec {
  background-image: url("../img/icon-minus.svg");
}
