.driver-transfer-card {
  display: block;
  min-height: 181px;
  border-radius: 4px;
  background-color: #ffffff;
  border: 1px solid @lightGrey;
  .md-block({
    position: relative;
    // min-height: 300px;
  });
}

.driver-transfer-card__content {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  border-bottom: 1px solid @lightGrey;

  .md-block({
    flex-direction: column;
    padding-top: 40px;
    border-bottom: none;
  });
}

.driver-transfer-card__subtitle {
  font-size: 12px;
  line-height: 14px;
  font-weight: 500;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  color: @grey;
}

.driver-transfer-card__to-from {
  margin-left: 16px;
  flex-grow: 1;

  .md-block({
    align-self: stretch;
    margin-left: 16px;
    margin-right: 16px;
    padding-bottom: 24px;
    border-bottom: 1px solid @lightGrey;
  });
}

.driver-transfer-card__driver {
  .md-block({
    display: none;
  });
}

.driver-transfer-card__info {
  margin-left: auto;
  padding-left: 24px;
  border-left: 1px solid @lightGrey;
  margin-top: 16px;
  margin-bottom: 16px;
  padding-top: 8px;
  padding-bottom: 8px;

  .md-block({
    display: none;
  });
}

.driver-transfer-card__places {

  .md-block({
    display: none;
  });
}

.driver-transfer-card__places .driver-transfer-card__subtitle {
  font-weight: 400;


}

.driver-transfer-card__places-list {
  display: flex;
}

.driver-transfer-card__price-375 {
  display: none;
  .md-block({
    display: inline-block;
  });
}

.driver-transfer-card__button {
  align-self: center;
  margin-right: 24px;

  a {
    width: 141px;
    height: 48px;
    box-sizing: border-box;

    .md-block({
      margin-left: 19px;
    });
  .md-block({
    margin-right: 0;
  });
  }

  .md-block({
    align-self: stretch;
    text-align: right;
    padding-top: 16px;
    padding-bottom: 16px;
    margin-right: 16px;
    margin-left: 16px;
    border-bottom: 1px solid @lightGrey;
  });
}


.driver-transfer-card__links {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 16px;
  padding-right: 16px;

  .md-block({
    justify-content: center;
    .tag-status {
      position: absolute;
      top: 0;
      right: 0;
      border-bottom-right-radius: 0;
      border-top-left-radius: 0;
    }
  });

}

