.profile-docs {
  box-sizing: border-box;
  width: 640px;


  .md-block({
    width: 100%;
  });
}

.profile-docs__form {
  padding: 32px 48px 48px 48px;
  border-radius: 4px;
  border: 1px solid @lightGrey;
  background-color: #ffffff;

  .md-block({
    padding: 24px 16px 48px 16px;
  });
}

.profile-docs__title {
  margin-bottom: 24px;

  .md-block({
    font-size: 24px;
  });
}


.profile-docs__subtitle {
  margin-bottom: 8px;
}

.profile-docs__desc {
  font-size: 14px;
  color: @grey;
  margin-bottom: 16px;

  .md-block({
    font-size: 16px;
  });
}

.profile-docs__files {
  display: flex;

  margin-bottom: 32px;

  .upload-photo {
    height: 136px;

    .md-block({
      height: 120px;
      width: 98px;
    });
  }

  .md-block({
    justify-content: space-between;
  });
}

.profile-docs__file {
  border: 1px solid @lightGrey;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  margin-right: 24px;

  .md-block({
    margin-right: 0;
  });
}

.profile-docs__file-prev {
  width: 136px;
  height: 136px;

  .md-block({
    width: 98px;
    height: 120px;
  });
}

.profile-docs__file-name {
  display: block;
  line-height: 32px;
  text-align: center;
  font-size: 12px;
  color: @lightDark;
  border-top: 1px solid @lightGrey;
}

.profile-docs__submit {
  margin: 0 auto;
  display: block;
}

