.driver-my-auto {
  width: 100%;
}
.driver-my-auto__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 32px;

  .md-block({
    flex-direction: column;

    .button:first-child {
      margin-bottom: 24px;
    }
  });
}

.driver-my-auto__content {
  border-radius: 4px;
  border: 1px solid @lightGrey;
  background-color: #ffffff;
  padding: 32px 48px;

  .lg-block({
    background-color: transparent;
    border: none;
    padding: 0;
  });
}

.driver-my-auto__item:not(:last-child) {
  margin-bottom: 24px;

  .lg-block({
    margin-bottom: 16px;
  });

  .md-block({
    margin-bottom: 24px;
  });
}
