
.driver-info {

  box-sizing: border-box;

  padding-top: 32px;
  padding-left: 48px;
  padding-right: 48px;

  display: flex;
  flex-direction: column;
  &::after {
    content: "";
    display: block;
    margin-top: 30px;
    height: 1px;
    background-color: @lightGrey;;
  }
}

.driver-info__title {
  margin-bottom: 14px;
}

.driver-info__inner {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
}

.driver-info__avatar {
  position: relative;
  width: 76px;
  height: 76px;
  border-radius: 50%;
  // overflow: hidden;

  img {
    width: 100%;
    height: auto;
  }
}

.driver-info__rating {
  // position: absolute;
  // bottom: -25px;
  // left: 0;
  justify-content: center;
  span {
    font-size: 14px;
  }
}

.driver-info__write-driver {
  // flex-basis: 100%;
  margin-left: 98px;

  .md-block({
    padding-right: 0;
  });
}

.driver-info__element {
  margin-right: 32px;

  span {
    display: block;
  }

  &:nth-child(2),
  &:nth-child(3) {
    display: inline-block;
  }
}

.driver-info__label {
  font-size: 12px;
  color: @grey;
}

.driver-info__value {
  font-size: 14px;
  color: @lightDark;
 }

 .driver-info__icons {
   display: flex;

   li {
     display: block;
     width: 32px;
     height: 32px;
     margin-left: 15px;
   }
 }
