.driver-view-transfer {
  box-sizing: border-box;
  margin: 0 auto;
  max-width: 520px;
  border-radius: 4px;
}



.driver-view-transfer__container {
  box-sizing: border-box;
  position: relative;
  background-color: #ffffff;
  padding: 24px 16px;
  border-radius: 4px;
  border: 1px solid @lightGrey;

}

.driver-view-transfer__wrapper-top {
  padding-bottom: 11px;
}

.driver-view-transfer__wrapper-data {
  display: flex;

  & > div:first-child {
    margin-right: auto;
  }
}

.driver-view-transfer__inner-top {
  display: flex;
  flex-direction: column;
  margin-bottom: 22px;

  &--1 {
    border-bottom: 1px solid @lightGrey;
  }
  &--2 {
    align-items: center;
    border-bottom: 1px solid @lightGrey;
    padding-bottom: 16px;
  }
}

.driver-view-transfer__from-to {
  margin-bottom: 16px;
  span {
    display: block;
  }
}

.driver-view-transfer__label {
  font-size: 12px;
  font-weight: 500;
  color: @grey;
  margin-bottom: 2px;
}

.driver-view-transfer__address {
  font-weight: 500;
  margin-bottom: 8px;
}
.driver-view-transfer__data {
  color: @grey;
}

.driver-view-transfer__prop {
  display: flex;
  align-items: center;
  & > span {
    margin-right: 16px;
  }
}

.driver-view-transfer__write {
  align-self: center;
  margin-bottom: 25px;
}

.driver-view-transfer__accept {
  display: block;
  box-sizing: border-box;
  width: 220px;
  margin-bottom: 26px;
}

.driver-view-transfer__hide {
  // margin-right: 22px;
}

.driver-view-transfer__inner-data--table-1 {
  margin-right: auto;
}

.driver-view-transfer__table {
  display: table;
  margin-bottom: 22px;
}

.driver-view-transfer__table-row {
  display: table-row;

  &:first-child {
    font-size: 14px;
    color: @dark;
    font-weight: 500;
  }
  &:not(:first-child) {
    font-size: 16px;
    color: @lightDark;
    font-weight: 400;
  }
}

.driver-view-transfer__table-cell {
  position: relative;
  display: table-cell;
  line-height: 19px;
  &:not(:last-child) {
    padding-right: 32px;
  }
  &:first-child {
    padding-right: 32px;
    padding-bottom: 8px;
  }

  .tag-status {
    box-sizing: border-box;
    width: 64px;
    position: absolute;
  }

}

.driver-view-transfer__places-list {
  position: absolute;
  display: flex;
}


.driver-view-transfer__wrapper-map h3 {
  padding-top: 32px;
  border-top: 1px solid @lightGrey;
  margin-bottom: 19px;
}

.driver-view-transfer__map {
  height: 281px;
  position: relative;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.driver-view-transfer__map-footer {
  width: 100%;
  background-color: @background;
  height: 48px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  padding-left: 48px;

  .driver-view-transfer__table {
    margin: 0;
  }

  .driver-view-transfer__table-row:first-child > div {
    font-size: 12px;
    color: @grey;
    padding-bottom: 4px;
  }
  .driver-view-transfer__table-row:last-child > div {
    font-size: 14px;
    color: @lightDark;
  }
}

.driver-view-transfer__map-pin {
  display: block;
  position: absolute;
  width: 24px;
  height: 24px;
  background-image: url("../img/iconfinder_pin_green.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  &--from {
    left: 27px;
    top: 39px;
  }
  &--to {
    right: 58px;
    top: 129px;
  }
}

.driver-view-transfer__wrapper-addit {
  border-bottom: 1px solid @lightGrey;
  margin-bottom: 16px;
}

.driver-view-transfer__table {
  margin-bottom: 16px;
}


.driver-view-transfer__wrapper-data--auto-view {

  .driver-view-transfer__inner-data:nth-child(2) {
    margin-left: auto;
    margin-right: auto;
  }
}
