
.additional-params__title {
  margin-bottom: 16px;
  line-height: 25px;
}

.additional-params__checks {
  li:not(:last-child) {
    margin-bottom: 14px;
  }
}

.additional-params__check {
  display: flex;
  align-items: center;
  span:last-child {
    padding-left: 8px;
    color: @lightDark;
    font-size: 16px;
    font-weight: 400;
  }

}
