.my-auto {
  border-radius: 4px;

  height: 160px;
  .lg-block({
    border: 1px solid @lightGrey;
    height: 140px;
    background-color: #ffffff;
  });

  .md-block({
    height: auto;
  });
}

.my-auto__inner {
  display: flex;
  align-items: center;
  height: 100%;
  padding-right: 24px;

  .md-block({
    flex-direction: column;
    align-items: stretch;
    padding: 0 16px;
    padding-bottom: 24px;
    // height: 100%;
  });
}

.my-auto__image {
  margin-right: auto;
  // width: 229px;
  flex-basis: 229px;
  align-self: stretch;
  flex-shrink: 0;
  overflow: hidden;
  border-radius: 4px;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .lg-block({
    flex-basis: 140px;
    margin-right: 24px;
  });

  .md-block({
    flex-basis: 120px;
    margin: 0 -16px;
    border-bottom-left-radius: 0;
    border-top-right-radius: 4px;
  });

}

.my-auto__content {
  box-sizing: border-box;
  margin-left: 24px;
  margin-right: 24px;
  padding-top: 24px;
  padding-bottom: 24px;
  align-self: stretch;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  .lg-block({
    padding-bottom: 16px;
    padding-top: 16px;
    margin-right: auto;
  });

  .md-block({
    // min-height: 243px;
    display: block;
    margin: 0;
    margin-bottom: 24px;
    padding: 24px 0;
    border-bottom: 1px solid @lightGrey;
  });
}

.my-auto__title {
  font-size: 20px;
  margin-bottom: 16px;

  .lg-block({
    font-size: 16px;
  });

  .md-block({
    font-size: 24px;
  });
}

.my-auto__stats {
  display: flex;
  align-items: center;
  .lg-block({
    flex-wrap: wrap;
    height: 100%;
  });
  // justify-content: space-between;
  .md-block({
    display: flex;
    margin-bottom: 16px;
  });
}

.my-auto__tags {
  .lg-block({
    flex-basis: 100%;
    margin-bottom: auto;
  });

  .md-block({
    margin-bottom: 16px;
  });
}

.my-auto__pass-bag {
  padding: 0 15px;
  margin-left: 16px;
  margin-right: 16px;
  border-left: 1px solid @lightGrey;
  border-right: 1px solid @lightGrey;

  .lg-block({
    margin-left: 0;
    padding-left: 0;
    border-left: 0;
  });

  & .pass-bag__pass,
  & .pass-bag__bag {
    .md-block({
      font-size: 16px;
      line-height: 16px;
      &::before {
        width: 20px;
        height: 20px;
      }
    });
  }

}

.my-auto__text {
  margin-top: auto;
  font-size: 12px;
  font-weight: 400;
  color: @lightDark;
  overflow: hidden;
  max-width: 401px;
  .lg-block({
    display: none;
  });

  .md-block({
    margin-top: 16px;
    padding-top: 16px;
    display: block;
    font-size: 14px;
    overflow: visible;
  })
}

.my-auto__icons {
  display: flex;
  margin: -4px;
  padding: 4px;

  li {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 4px;
    width: 16px;
    height: 16px;

    .md-block({
      margin: 7px;
      width: 16px;
      height: 16px;
      svg {
        width: 100%;
        height: auto;
      }
    });
  }

}

.my-auto__price-block {
  margin-left: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  .md-block({
    margin: 0;
  });
}

.my-auto__more {
  box-sizing: border-box;
  width: 192px;
  margin-bottom: 18px;

  .md-block({
    width: 100%;
  });
}

.my-auto__addit-chars {
  display: none;
  box-sizing: border-box;
  width: calc(100% + 16px);
  flex-wrap: wrap;
  font-size: 16px;
  color: @grey;
  margin-right: -16px;

  li {
    margin-right: 16px;
    margin-bottom: 6px;
  }
}
.my-auto__addit-tags {
  display: none;
}

// 8-1 Driver My Auto
.my-auto--basic {

  .md-block({

    padding: 16px;
    padding-bottom: 25px;

    .my-auto__text {
      display: none;
    }

    .my-auto__icons {
      display: none;
    }

    .my-auto__stats {
      display: none;
    }

    .my-auto__title {
      font-size: 18px;
    }

    .my-auto__inner {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      padding: 0;

    }

    .my-auto__content {
      flex-basis: 100px;
      flex-grow: 1;
      display: block;
      padding: 0;
      margin: 0;
      border-bottom: none;
    }

    .my-auto__image {
      width: 96px;
      height: 96px;
      margin-right: 16px;
      margin-left: 0;
      border-radius: 4px;
    }

    .my-auto__addit-tags {
      display: block;
      width: 100%;
      padding: 24px 0;
      margin: 24px 0;
      border-top: 1px solid @lightGrey;
      border-bottom: 1px solid @lightGrey;

      .my-auto__tags {
        // display: block;
        margin: 0;
      }
    }

    .my-auto__price-block {
      width: 100%;
    }

    .my-auto__addit-chars {
      display: flex;
    }

  });
}


// 8-3 Driver Auto History

.my-auto--history {

  .my-auto__content {
    padding-top: 8px;
  }
  .my-auto__image {
    margin-right: 24px;
    width: 229px;
    height: 160px;
  }


  .lg-block({

    height: auto;
    border: none;

    .my-auto__image {
      width: 229px;
      height: 160px;
      flex-basis: auto;
    }

    .my-auto__content {
      padding: 0;
      justify-content: flex-start;
    }

    .my-auto__text {
      display: block;
      width: 100%;
    }

    .my-auto__title {
      margin-bottom: 13px;
    }

    .my-auto__stats {
      height: auto;
    }

    .my-auto__tags {
      margin-bottom: 8px;
    }
  });



  .md-block({
    .my-auto__inner {
      display: block;
      padding-bottom: 24px;
      padding: 0;
      margin: 0 -16px;
    }

    .my-auto__content {
      display: flex;
      flex-direction: column;
      padding-left: 16px;
      padding-right: 16px;
      border-bottom: none;
      margin-bottom: 0;
    }

    .my-auto__image {
      width: 100%;
      height: 205px;
      margin: 0;
      margin-bottom: 24px;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }

    .my-auto__icons,
    .my-auto__pass-bag {
      display: none;
    }

    .my-auto__title {
      margin-bottom: 16px;
    }

    .my-auto__addit-chars {
      display: flex;
      align-items: center;
      margin-bottom: 24px;
    }

    .my-auto__stats {
      border-top: 1px solid @lightGrey;
      padding-top: 16px;
      order: 2;
    }

    .my-auto__text {
      order: 2;
      margin-top: 0;
      padding-top: 0;
    }
  });
}
