
.profile-info {
  box-sizing: border-box;
  width: 640px;
  padding: 32px 32px 48px 32px;
  border-radius: 4px;
  border: 1px solid @lightGrey;
  background-color: #ffffff;

  .lg-block({
    // margin-left: 0;
  });

  .md-block({
    width: 100%;
    padding: 24px 16px 48px 16px;
  });
}

.profile-info__title {
  margin-bottom: 24px;
}

.profile-info__inner {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;

  & > div:not(:last-child) {
    width: 220px;
  }
  & > div:last-child {
    width: 104px;
  }

  .md-block({
    display: block;

    & > div:not(:last-child) {
      width: 100%;
      margin-bottom: 16px;
    }
    & > div:last-child {
      width: 50%;
    }
  });
}

.profile-info__email {
  margin-bottom: 16px;
}

.profile-info__phone {
  margin-bottom: 40px;
  & > div {
    display: flex;
    & > div:first-child {
      width: 160px;
      margin-right: 14px;
    }
    & > div:last-child {
      flex-grow: 1;
    }
  }

  .md-block({
    & > div {
      display: block;
      & > div:first-child {
        width: 50%;
        margin-right: 0;
        margin-bottom: 16px;
      }
      & > div:last-child {
        width: 100%;
      }
    }
  });
}

.profile-info__text {
  margin-bottom: 16px;

  textarea {
    box-sizing: border-box;
    border-radius: 4px;
    border: 1px solid @lightGrey;
    padding: 16px 24px;
    font-size: 16px;
    height: 111px;
    resize: none;
    overflow: hidden;

    &::placeholder {
      color: @grey;
    }
    .md-block({
      height: 164px;
    });
  }
}

.profile-info__alarm {
  padding: 12px;
  color: @grey;
  text-align: center;
  font-size: 14px;
  font-weight: 500;
  border-radius: 4px;
  border: 1px solid @yellow;
  margin-bottom: 48px;

  .md-block({
    padding: 8px;
  });
}

.profile-info__submit {
  margin: 0 auto;
  display: block;
}
