.profile-photo {
  box-sizing: border-box;
  width: 640px;
  // margin-left: 32px;
  padding: 32px 48px 48px 48px;
  border-radius: 4px;
  border: 1px solid @lightGrey;
  background-color: #ffffff;

  .lg-block({
    // margin-left: 0;
  });

  .md-block({
    width: 100%;
    padding: 24px 16px 48px 16px;
  });
}

.profile-photo__title {
  margin-bottom: 16px;
}

.profile-photo__desc {
  color: @grey;
  font-size: 16px;
  line-height: 26px;
  margin-bottom: 24px;
}
.profile-photo__inner {
  display: flex;
  align-items: flex-start;

  .md-block({
    flex-direction: column-reverse;
  });
}

.profile-photo__upload-block {
  flex-basis: 50%;

  .md-block({
    flex-basis: auto;
    width: 100%;
  });
}
.profile-photo__example {
  flex-basis: 50%;
  box-sizing: border-box;
  padding: 16px;
  border-radius: 4px;
  border: 1px solid @yellow;

  ul {
    font-size: 12px;
    line-height: 19px;
    font-weight: 300;
    color: @lightDark;
    margin-bottom: 16px;
  }

  .md-block({
    flex-basis: auto;
    width: 100%;
    margin-bottom: 24px;
    padding-left: 32px;
    padding-right: 32px;
  });
}

.profile-photo__subtitle {
  font-size: 16px;
  color: @dark;
  margin-bottom: 12px;
}

.profile-photo__sample {
  display: flex;

  div {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    overflow: hidden;
    position: relative;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &::after {
      content: "";
      position: absolute;
      width: 16px;
      height: 16px;
      top: 0;
      right: 0;
      background-image: url("../img/checkbox.svg");
      background-position: center;
      background-repeat: no-repeat;
    }
  }

  p {
    color: @grey;
    font-size: 14px;
    margin-left: 16px;
  }
}

.profile-photo__upload-block {
  button[type="reset"] {
    margin: 0 auto;
    display: block;
    border-bottom-color: @blue;
    margin-bottom: 47px;
    span {
      color: @blue;
    }
  }

  button[type="submit"] {
    margin: 0 auto;
    display: block;
  }

  .upload-photo {
    border-radius: 50%;
    margin: 0 auto;
    width: 128px;
    height: 128px;
    margin-bottom: 7px;
  }
}
