.banner {
  flex-grow: 1;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  flex-direction: column;
  // align-items: center;

  .lg-block({
    // width: 768px;
  });

  .md-block({
    // width: 375px;
  });
}
.banner__wrapper {
  position: relative;
}

.banner__about {
  align-self: flex-end;
  display: flex;
  align-items: center;
  background-color: #ffffff;
  box-sizing: border-box;
  width: 45%;
  padding: 32px 48px 24px 48px;
  text-decoration: none;

  svg {
    transition-duration: 0.5s;
  }

  &:hover {
    & svg {
      transform: scale(1.2);
      transition-duration: 0.5s;
    }
  }

  & > div {
    margin-left: 24.3px;
  }

  & > div span {
    display: block;
  }

  & b {
    font-size: 18px;
    font-weight: 500;
    color: @dark;
  }

  .lg-block({
    width: 364px;
  });

  .md-block({
    display: none;
  })


}

.banner__more {
  font-size: 14px;
  font-weight: 500;
  color: @lightDark;
}

.banner__search-form {
  margin-top: 96px;

  .md-block({
    // display: none;
  });
}
