.menu-icon {
  height: 24px;
  cursor: pointer;
  width: 24px;
  // position: relative;

  .bar {
    width: 100%;
    height: 3px;
    background-color: @green; //change on your own mobile menu color
    border-radius: 4px;

    &-1 {
      position: absolute;
      top: 3px;
      left: 0;
    }

    &-2 {

      position: absolute;
      top: calc(50% - 2px);;
      left: 0;
    }

    &-3 {

      position: absolute;
      bottom: 3px;
      left: 0;
    }
  }
}

.menu-icon {
  transition: all 0.2s linear 0s;

  .bar {
    transition: all 0.2s linear 0.2s
  }

  &.active {
    transform: rotate(90deg);

    .bar {

      &-2 {
        opacity: 0;
      }

      &-1 {
        transform: rotate(45deg);
        top: calc(50% - 2px);
      }

      &-3 {
        transform: rotate(-45deg);
        top: calc(50% - 2px);
        bottom: auto;
      }
    }
  }
}
