@charset "UTF-8";
@font-face {
  font-weight: 500;
  font-family: "Geometria";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/geometria-medium.woff") format("woff");
}
@font-face {
  font-weight: 700;
  font-family: "Geometria";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/geometria-bold.woff") format("woff");
}
@font-face {
  font-weight: 500;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoMedium.woff") format("woff");
}
@font-face {
  font-weight: 400;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoRegular.woff") format("woff");
}
@font-face {
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoCondensedRegular.woff") format("woff");
}
body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
fieldset,
img,
abbr {
  border: 0;
}
address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}
ul li {
  list-style: none;
}
caption,
th {
  text-align: left;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
sup {
  vertical-align: text-top;
}
sub {
  vertical-align: text-bottom;
}
input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}
legend {
  color: #000;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}
img {
  max-width: 100%;
  height: auto;
}
.wrapper {
  margin: 0 auto;
  padding-left: 160px;
  padding-right: 160px;
  width: 100%;
  box-sizing: border-box;
}
@media (max-width: 1439px) {
  .wrapper {
    padding-left: 32px;
    padding-right: 32px;
  }
}
@media (max-width: 767px) {
  .wrapper {
    padding-left: 16px;
    padding-right: 16px;
  }
}
body {
  min-width: 375px;
  margin: 0 auto;
  min-height: 100vh;
  font-size: 18px;
  line-height: 1.35em;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  color: #3C3E4B;
  font-style: normal;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #F8FAFA;
}
h1,
h2,
h3,
h4 {
  font-family: "Geometria", Arial, Helvetica, sans-serif;
  font-weight: 500;
  line-height: 1em;
  color: #3C3E4B;
}
h1 {
  font-size: 32px;
}
@media (max-width: 767px) {
  h1 {
    font-size: 28px;
    font-weight: 700;
  }
}
h2 {
  font-size: 24px;
}
h3 {
  font-size: 20px;
}
@media (max-width: 767px) {
  h3 {
    font-size: 18px;
    font-weight: 700;
  }
}
h4 {
  font-size: 16px;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.hidden {
  display: none !important;
}
body *:focus {
  outline: none;
}
input:focus {
  outline: none;
}
button:focus {
  outline: none;
}
.price-v2 {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.price-v2__label {
  font-size: 14px;
  color: #B0B0B0;
}
.price-v2__value {
  padding-left: 1em;
  font-size: 20px;
  font-weight: 500;
  color: #3C3E4B;
}
@media (max-width: 767px) {
  .price-v2__value {
    font-weight: 400;
  }
}
.price-v1 {
  text-align: right;
  line-height: 1em;
  font-weight: 500;
  font-size: 18px;
  color: #3C3E4B;
}
.price-v1 span:last-child {
  font-size: 14px;
  font-weight: 400;
  color: #B0B0B0;
}
.additional-params__title {
  margin-bottom: 16px;
  line-height: 25px;
}
.additional-params__checks li:not(:last-child) {
  margin-bottom: 14px;
}
.additional-params__check {
  display: flex;
  align-items: center;
}
.additional-params__check span:last-child {
  padding-left: 8px;
  color: #525463;
  font-size: 16px;
  font-weight: 400;
}
.input {
  display: flex;
  flex-direction: column;
}
.input--select {
  position: relative;
}
.input--select::after {
  content: "";
  display: block;
  position: absolute;
  width: 20px;
  height: 20px;
  background-image: url("../img/icon-arrow-down.svg");
  background-repeat: no-repeat;
  background-position: center;
  right: 14px;
  top: calc(50% - 10px);
  cursor: pointer;
}
.input--select > input::placeholder {
  color: #B0B0B0;
}
.input--date {
  position: relative;
}
.input--date::after {
  content: "";
  display: block;
  position: absolute;
  width: 20px;
  height: 20px;
  background-image: url("../img/icon-arrow-down.svg");
  background-repeat: no-repeat;
  background-position: center;
  right: 14px;
  top: calc(50% - 10px);
  cursor: pointer;
}
.input--date > input::placeholder {
  color: #B0B0B0;
}
.range__values {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.range__values > span {
  display: block;
  width: 12px;
  height: 1px;
  margin: 0 1px;
  background-color: #525463;
}
.range__values input {
  box-sizing: border-box;
  max-width: 88px;
  text-align: center;
  padding-left: 0;
  padding-right: 0;
}
.range__price-values {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.range__price-values > span {
  display: block;
  width: 12px;
  height: 1px;
  margin: 0 7px;
  background-color: #525463;
}
.range__price-values input {
  box-sizing: border-box;
  width: 80px;
  text-align: center;
  padding: 7px 7px;
  margin: 0;
}
.range-1 {
  border-radius: 0 !important;
  border: none !important;
  height: 4px !important;
  background-color: #E8EBF0 !important;
}
.range-1 .ui-slider-range {
  background-color: #9EDBA1;
  border-radius: 0 !important;
}
.range-1 .ui-slider-handle {
  width: 20px !important;
  height: 20px !important;
  border-radius: 50%;
  background-color: #4BAF4F !important;
  top: calc(50% - 10px) !important;
  border: none !important;
}
.range-1 .ui-slider-handle::after {
  content: "";
  display: block;
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  top: calc(50% - 3px);
  left: calc(50% - 3px);
  background-color: #ffffff;
}
.range-2 {
  border-radius: 0 !important;
  border: none !important;
  height: 4px !important;
  background-color: #E8EBF0 !important;
}
.range-2 .ui-slider-range {
  background-color: #9EDBA1;
  border-radius: 0 !important;
}
.range-2 .ui-slider-handle {
  width: 20px !important;
  height: 20px !important;
  border-radius: 50%;
  background-color: #4BAF4F !important;
  top: calc(50% - 10px) !important;
  border: none !important;
}
.range-2 .ui-slider-handle::after {
  content: "";
  display: block;
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  top: calc(50% - 3px);
  left: calc(50% - 3px);
  background-color: #ffffff;
}
.field__range-block > label {
  margin-bottom: 16px !important;
}
.range .ui-slider-range {
  z-index: auto !important;
}
.range .ui-slider-handle {
  z-index: auto !important;
}
.btn-price {
  width: 141px;
  display: flex;
  flex-direction: column;
}
.btn-price__price {
  margin-bottom: 8px;
  padding-right: 5px;
  text-align: right;
  line-height: 1em;
  font-weight: 500;
  font-size: 18px;
  color: #3C3E4B;
}
.btn-price__price span:last-child {
  font-size: 14px;
  font-weight: 400;
  color: #B0B0B0;
}
.pass-bag {
  width: 72px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.pass-bag__pass {
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  color: #525463;
  display: flex;
  align-items: center;
}
.pass-bag__pass::before {
  content: "";
  display: inline-block;
  width: 18px;
  height: 18px;
  background-image: url("../img/icon-users-solid.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.pass-bag__bag {
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  color: #525463;
  display: flex;
  align-items: center;
}
.pass-bag__bag::before {
  content: "";
  display: inline-block;
  width: 18px;
  height: 18px;
  background-image: url("../img/icon-baggage.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.tags__list {
  list-style: none;
  display: flex;
  margin: -4px;
  padding: 4px;
}
.tags__item {
  display: inline-block;
  margin: 4px;
  padding: 5px 12px;
  font-size: 11px;
  font-weight: 500;
  line-height: 1em;
  color: #525463;
  text-align: center;
  text-transform: uppercase;
  background-color: #E8EBF0;
  border-radius: 50px;
}
.options__item {
  position: relative;
  padding-left: 33px;
  margin-bottom: 13px;
  font-size: 16px;
  line-height: 19px;
}
.options__item--conditioner .options__icon {
  background-image: url("../img/icon-conditioner.svg");
}
.options__item--music .options__icon {
  background-image: url("../img/icon-music.svg");
}
.options__item--pets .options__icon {
  background-image: url("../img/icon-pets.svg");
}
.options__item--no-pets .options__icon {
  background-image: url("../img/icon-NOpets.svg");
}
.options__item--no-smoking .options__icon {
  background-image: url("../img/icon-NOsmoking.svg");
}
.options__item--smoking .options__icon {
  background-image: url("../img/icon-smoking.svg");
}
.options__icon {
  display: block;
  position: absolute;
  width: 24px;
  height: 24px;
  left: 0;
  top: calc(50% - 12px);
  background-position: center;
  background-repeat: no-repeat;
}
.drop-list {
  box-sizing: border-box;
  display: block;
  width: 140px;
  height: 200px;
  border-radius: 4px;
  background-color: #ffffff;
}
.drop-list {
  border-radius: 10px;
  background: #efefef;
  border: 1px solid #777;
  position: absolute;
  opacity: 1;
  top: 200px;
  left: 500px;
}
.drop-list::before,
.drop-list::after {
  content: "";
  display: block;
  position: absolute;
  box-sizing: border-box;
  top: -11px;
  right: 20px;
  width: 20px;
  height: 20px;
  background: #ffffff;
  border: 1px solid #777;
  transform: rotate(45deg);
}
.drop-list::before {
  background: #ffffff;
}
.table-block {
  display: inline-block;
}
.table-block__head {
  display: block;
  font-size: 12px;
  line-height: 14px;
  color: #B0B0B0;
}
.table-block__data {
  display: block;
  font-size: 12px;
  line-height: 14px;
  color: #525463;
}
.page-multiselector {
  display: flex;
  height: auto;
  line-height: auto;
}
.page-multiselector li:first-child span {
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
.page-multiselector li:last-child span {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}
@media (max-width: 767px) {
  .page-multiselector {
    width: 100%;
  }
  .page-multiselector li {
    flex-grow: 1;
  }
}
.page-multiselector__radio {
  font-size: 12px;
  line-height: 14px;
  font-weight: 500;
}
.page-multiselector__radio span {
  box-sizing: border-box;
  display: block;
  text-align: center;
  padding-top: 9px;
  padding-bottom: 8px;
  padding-left: 16px;
  padding-right: 16px;
  border: 1px solid #E8EBF0;
  color: #B0B0B0;
  background-color: #ffffff;
  width: 176px;
}
@media (max-width: 767px) {
  .page-multiselector__radio span {
    width: auto;
  }
}
.page-multiselector__radio input:checked + span {
  color: #ffffff;
  border-color: #4BAF4F;
  background-color: #4BAF4F;
}
.page-multiselector-2 {
  display: flex;
  height: auto;
  line-height: auto;
}
@media (max-width: 767px) {
  .page-multiselector-2 {
    width: 100%;
  }
  .page-multiselector-2 li {
    flex-grow: 1;
  }
}
.page-multiselector-2__radio {
  font-size: 14px;
  line-height: 14px;
  font-weight: 500;
}
.page-multiselector-2__radio span {
  box-sizing: border-box;
  display: block;
  text-align: center;
  padding-top: 9px;
  padding-bottom: 9px;
  padding-left: 16px;
  padding-right: 16px;
  border-bottom: 1px solid #E8EBF0;
  color: #B0B0B0;
  width: 100%;
}
@media (max-width: 767px) {
  .page-multiselector-2__radio span {
    width: auto;
  }
}
.page-multiselector-2__radio input:checked + span {
  color: #4BAF4F;
  padding-bottom: 8px;
  border-bottom: 2px solid #4BAF4F;
}
.booking-auto-card {
  display: block;
  height: 173px;
  border-radius: 4px;
  background-color: #ffffff;
  border: 1px solid #E8EBF0;
}
.booking-auto-card__content {
  display: flex;
  height: 128px;
  border-bottom: 1px solid #E8EBF0;
}
@media (max-width: 767px) {
  .booking-auto-card__content {
    flex-direction: column;
  }
}
.booking-auto-card__status {
  position: relative;
  display: inline-block;
  width: 32px;
  margin-left: auto;
  border-top-right-radius: 4px;
  background-position: center;
  background-repeat: no-repeat;
}
.booking-auto-card__status::after {
  content: "";
  position: absolute;
  display: block;
  width: 24px;
  height: 24px;
  left: calc(50% - 12px);
  top: calc(50% - 12px);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-image: url("../img/checked-white.svg");
}
@media (max-width: 767px) {
  .booking-auto-card__status {
    order: -1;
    align-self: flex-end;
    height: 32px;
  }
  .booking-auto-card__status::before {
    content: "";
    display: inline;
    padding-right: 8px;
    color: #4BAF4F;
    font-size: 16px;
    line-height: 32px;
    font-weight: 400;
    position: absolute;
    top: 0;
    left: -220px;
  }
}
.booking-auto-card__status--confirmed {
  background-color: #4BAF4F;
}
@media (max-width: 767px) {
  .booking-auto-card__status--confirmed::before {
    content: "Водитель подтвердил бронь";
  }
}
.booking-auto-card__status--canceled {
  background-color: #E55739;
}
.booking-auto-card__status--awaiting {
  background-color: #FFBE33;
}
@media (max-width: 767px) {
  .booking-auto-card__driver {
    display: none;
  }
}
@media (max-width: 767px) {
  .booking-auto-card__info {
    display: none;
  }
}
.booking-auto-card__places-list {
  display: flex;
}
.booking-auto-card__links {
  display: flex;
  align-items: center;
}
@media (max-width: 767px) {
  .booking-auto-card__links {
    display: none;
  }
}
.booking-auto-card__more {
  display: none;
}
@media (max-width: 767px) {
  .booking-auto-card__more {
    display: block;
  }
}
.booking-transfer-card {
  display: block;
  height: 173px;
  border-radius: 4px;
  background-color: #ffffff;
  border: 1px solid #E8EBF0;
}
@media (max-width: 767px) {
  .booking-transfer-card {
    height: auto;
    padding-bottom: 24px;
  }
}
.booking-transfer-card .pass-bag__pass::before,
.booking-transfer-card .pass-bag__bag::before {
  width: 24px;
  height: 24px;
}
.booking-transfer-card__content {
  display: flex;
  height: 128px;
  align-items: center;
  border-bottom: 1px solid #E8EBF0;
  padding-left: 32px;
}
@media (max-width: 1439px) {
  .booking-transfer-card__content {
    padding-left: 16px;
  }
}
@media (max-width: 767px) {
  .booking-transfer-card__content {
    height: auto;
    flex-direction: column;
    align-items: stretch;
    padding-left: 0;
    border-bottom: none;
  }
}
.booking-transfer-card__to-from {
  margin-right: 24px;
}
.booking-transfer-card__to-from .cards-direct__cities {
  width: 206px;
}
@media (max-width: 1439px) {
  .booking-transfer-card__to-from .cards-direct__cities {
    width: 174px;
  }
}
@media (max-width: 1439px) {
  .booking-transfer-card__to-from {
    margin-right: 16px;
  }
}
@media (max-width: 767px) {
  .booking-transfer-card__to-from {
    margin-right: 16px;
    margin-left: 16px;
    margin-bottom: 24px;
    padding-bottom: 24px;
    border-bottom: 1px solid #E8EBF0;
  }
}
.booking-transfer-card__status {
  position: relative;
  margin-left: auto;
  border-top-right-radius: 4px;
  align-self: stretch;
}
.booking-transfer-card__status span {
  display: none;
  font-size: 16px;
}
.booking-transfer-card__status::after {
  content: "";
  border-top-right-radius: 4px;
  display: block;
  width: 32px;
  height: 100%;
  left: calc(50% - 12px);
  top: calc(50% - 12px);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 24px;
}
.booking-transfer-card__status .field__dropdown-contain {
  left: -360px;
}
.booking-transfer-card__status:hover > .field__dropdown-contain .field__drop-out {
  margin-top: 16px !important;
}
.booking-transfer-card__status--confirmed span {
  color: #4BAF4F;
}
.booking-transfer-card__status--confirmed::after {
  background-color: #4BAF4F;
  background-image: url("../img/checked-white.svg");
}
.booking-transfer-card__status--awaiting span {
  color: #FFBE33;
}
.booking-transfer-card__status--awaiting::after {
  background-color: #FFBE33;
  background-image: url("../img/waiting.svg");
}
.booking-transfer-card__status--canceled span {
  color: #E55739;
}
.booking-transfer-card__status--canceled::after {
  background-color: #E55739;
  background-image: url("../img/canceled.svg");
}
@media (max-width: 1439px) {
  .booking-transfer-card__status::after {
    width: 26px;
    background-size: 18px;
  }
}
@media (max-width: 767px) {
  .booking-transfer-card__status {
    order: -1;
    align-self: flex-end;
    display: flex;
    align-items: center;
    margin-bottom: 16px;
  }
  .booking-transfer-card__status span {
    display: inline;
    padding-right: 8px;
  }
  .booking-transfer-card__status::after {
    height: 32px;
    width: 32px;
  }
  .booking-transfer-card__status .field__dropdown-contain {
    display: none;
  }
}
.booking-transfer-card__driver {
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
}
.booking-transfer-card__driver::before,
.booking-transfer-card__driver::after {
  content: "";
  display: block;
  width: 1px;
  height: 96px;
  background-color: #E8EBF0;
}
.booking-transfer-card__driver::before {
  margin-right: 32px;
}
.booking-transfer-card__driver::after {
  margin-left: 32px;
}
@media (max-width: 1439px) {
  .booking-transfer-card__driver::before {
    margin-right: 16px;
  }
  .booking-transfer-card__driver::after {
    margin-left: 16px;
  }
}
@media (max-width: 767px) {
  .booking-transfer-card__driver {
    display: none;
  }
