}
.popup__close {
  display: block;
  position: absolute;
  overflow: hidden;
  width: 25px;
  height: 25px;
}
.popup__close::before,
.popup__close::after {
  content: " ";
  display: block;
  position: absolute;
  width: 25px;
  height: 2px;
  background: #B0B0B0;
  margin: 12px 0 0 0;
}
