/* Corner radius */
.ui-corner-all,
.ui-corner-bottom,
.ui-corner-left,
.ui-corner-bl {
  border-bottom-left-radius: 4px;
}
.ui-corner-all,
.ui-corner-bottom,
.ui-corner-right,
.ui-corner-br {
  border-bottom-right-radius: 4px;
}
/* Overlays */
.ui-widget-overlay {
  background: #aaaaaa;
  opacity: 0.3;
  filter: alpha(opacity=30);
  /* support: IE8 */
}
.ui-widget-shadow {
  -webkit-box-shadow: 0px 0px 5px #666666;
  box-shadow: 0px 0px 5px #666666;
}
div.vote-wrap {
  overflow: hidden;
}
div.vote-block {
  position: relative;
}
div.vote-hover {
  float: left;
  cursor: pointer;
}
div.vote-stars {
  position: absolute;
  left: 0;
  top: 0;
}
div.vote-active {
  position: absolute;
  left: 0;
  top: 0;
}
div.vote-result {
  color: #ccc;
  font-size: 14px;
  margin-top: 12px;
  padding-left: 190px;
}
div.vote-success {
  clear: both;
  color: #ccc;
  font-size: 14px;
  margin: 10px 0 0 10px;
}
