
.driver-request-transfers {
  width: 100%;
}

.driver-request-transfers__controls {
  margin-bottom: 32px;
}

.driver-request-transfers__list {
  display: none;
  .lg-block({
    display: block;
  });
}

.driver-request-transfers__item {
  margin-bottom: 16px;

  .md-block({
    margin-bottom: 24px;
  });
}

.driver-request-transfer__table {
  border-radius: 4px;
  background-color: #ffffff;
  border: 1px solid @lightGrey;

  .lg-block({
    display: none;
  });
}

.request-transfer-table__more {
  box-sizing: border-box;
  width: 138px;
}

.driver-request-transfers {
  .driver-transfer-card {

    .lg-block({
      height: auto;
      box-sizing: border-box;

      .driver-transfer-card__places {
        width: 121px;
        flex-shrink: 0;
      }
      .cards-direct__time-date {
        text-align: right;
      }
    });
    .md-block({
      height: auto;
      // min-height: 300px;
      .cards-direct__cities {
        font-size: 16px;
        font-weight: 500;
      }
      .cards-direct__time-date {
        font-size: 16px;
        text-align: right;
        width: 96px;
        flex-shrink: 0;
      }
    });
  }
  .driver-transfer-card__footer {
    padding-top: 10px;
    padding-bottom: 10px;

    .md-block({
      padding-top: 18px;
      padding-bottom: 16px;
    });
  }
}


