
.route-info {

  box-sizing: border-box;

  padding-top: 32px;
  padding-left: 48px;
  padding-right: 48px;

  display: flex;
  flex-direction: column;
  &::after {
    content: "";
    display: block;
    margin-top: 30px;
    height: 1px;
    background-color: @lightGrey;;
  }
}

.route-info__title {
  margin-bottom: 24px;

  .lg-block({
    font-size: 20px;
  });
}

.route-info__inner {
  display: flex;
}

.route-info__to-from {
  display: flex;
  flex-grow: 1;
  margin-right: 48px;
  height: 110px;
}

.route-info__time-date {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  text-align: right;
  color: @lightDark;
}

.route-info__icon-direct {
  height: 110px;
  width: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: center;


}

.route-info__cities {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  font-size: 14px;
  line-height: 16px;
  color: @dark;
}

.route-info__city {
  position: relative;

  span:first-child {
    font-size: 14px;
    font-weight: 500;
  }

  span:last-child {
    padding: 0;
    position: absolute;
    bottom: -20px;
    left: 0;
    font-size: 12px;
    font-weight: 400;
    width: 250px;
  }

  &--from {
    span:last-child {
      color: @green;
    }
  }
  &--to {
    span:last-child {
      color: @yellow;
    }
  }
}

.route-info__map {

  margin-left: auto;
  display: flex;
  flex-direction: column;

  & > div {
    width: 163px;
    height: 104px;
  }

  img {
    display: block;
    width: 100%;
    height: auto;
  }
}

.route-info__button-map {
  align-self: center;
  color: @blue;
  border-bottom-color: @blue;
}

.route-info__companies {
  display: inline-flex;
}
