.lk-header {
  box-sizing: border-box;
  position: sticky;
  top: 0;
  width: 100%;
  height: 64px;
  padding: 8px 24px;
  padding-right: 40px;
  display: flex;
  align-items: center;
  background-color: #ffffff;
  border-bottom: 1px solid @lightGrey;
  z-index: 800;


  .lg-block({
    padding-left: 16px;
    padding-right: 32px;
  });
  .md-block({
    padding-right: 48px;
  });
}

.lk-header__btn-back {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  color: @grey;
  text-decoration: none;
  font-size: 18px;
  line-height: 1em;
  margin-right: 72px;

  .lg-block({
    margin-right: 68px;
  });

  &::before {
    content: "";
    display: block;
    width: 18px;
    height: 18px;
    margin-right: 8px;
    background-image: url("../img/arrow-point-to-right.svg");
    background-position: center;
    background-repeat: no-repeat;
  }
}

.lk-header__title-separate {
  display: block;
  align-self: stretch;
  width: 1px;
  background-color: @lightGrey;

  .md-block({display: none;});
}

.lk-header__title {
  overflow: hidden;
  white-space: pre;
  margin-left: 24px;
  margin-right: 24px;

  .lg-block({
    margin-left: 16px;
    margin-right: 16px;
    font-size: 20px;
  });

  .md-block({
    display: none;
    // white-space: normal;
    // position: absolute;
    // bottom: -68px;
    // left: 0;

    // font-size: 28px;
  });
}

.lk-header__notification {
  position: relative;
  margin-right: 44px;

  .lg-block({
    margin-right: 33.4px;
  });

  .lk-header__notification-count {
    position: absolute;
    top: -50%;
    right: -50%;
    display: block;
    width: 20px;
    height: 20px;
    background-color: @red;
    border-radius: 3px;
    color: #ffffff;
    font-size: 12px;
    font-weight: 500;
    line-height: 20px;
    text-align: center;
  }
}

.lk-header__user-block {
  display: flex;
  align-items: center;
  margin-left: auto;
}
.lk-header__user-nav {
  display: flex;
  align-items: center;
  position: relative;

  .md-block({
    display: none;
  });
}

