.lk-header__open-popup {
  position: fixed;
  right: 24px;
  top: 24px;
  width: 24px;
  height: 24px;
  display: none;
  .md-block({
    display: block;
  });
  z-index: 1100;
}

.lk-header__open-popup.active {
  position: absolute;
}

.lk-header__usermenu-open {
  .md-block({
    display: none;
  });
}

.lk-header__username {
  font-size: 14px;
  color: @lightDark;
  margin-right: 12px;

  .lg-block({
    display: none;
  });
}

.lk-header__avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  .md-block({
    display: none;
  });
}

.lk-header__notification {

  .md-block({
    margin-top: 10px;
  });
  .field__dropdown-contain {
    // width: 300px;
    // height: 200px;
    left: -365px;
  }

}

.lk-header__menu {
  padding: 8px 16px 4px 16px;

  & > li {
    display: block;
    width: 104px;
    padding-top: 4px;
    padding-bottom: 8px;
  }

  & > li:last-child {
    border-top: 1px solid @lightGrey;

  }

  a {
    display: block;
    font-size: 14px;
    color: @lightDark;
    text-decoration: none;

    &:hover {
      opacity: 0.6;
    }
  }
}

.lk-header__user-nav .field__dropdown-contain {
  min-width: 0;
  right: 0;
}
