@font-face {
  font-weight: 500;
  font-family: "Geometria";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/geometria-medium.woff") format("woff");
}

@font-face {
  font-weight: 700;
  font-family: "Geometria";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/geometria-bold.woff") format("woff");
}

@font-face {
  font-weight: 500;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoMedium.woff") format("woff");
}

@font-face {
  font-weight: 400;
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoRegular.woff") format("woff");
}

@font-face {
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/RobotoCondensedRegular.woff") format("woff");
}

.reset();

.wrapper {
  margin: 0 auto;
  padding-left: 160px;
  padding-right: 160px;
  width: 100%;
  // width: 1440px;
  // max-width: 1920px;
  box-sizing: border-box;
  // .llg-block({
  //   padding-left: 32px;
  //   padding-right: 32px;
  // });
  .lg-block({
    // width: 768px;
    // max-width: 1240px;
    padding-left: 32px;
    padding-right: 32px;
  });

  .md-block({
    // width: 375px;
    // max-width: 480px;
    // min-width: 320px;
    padding-left: 16px;
    padding-right: 16px;
  });
}

body {
  min-width: 375px;
  // max-width: 1920px;
  margin: 0 auto;
  min-height: 100vh;
  font-size: 18px;
  line-height: 1.35em;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  color: @dark;
  font-style: normal;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  // overflow: hidden;

  background-color: @background;
}

h1, h2, h3, h4 {
  font-family: "Geometria", Arial, Helvetica, sans-serif;
  font-weight: 500;
  line-height: 1em;
  color: @dark;
}

h1 {
  font-size: 32px;

  .md-block({
    font-size: 28px;
    font-weight: 700;
  });
}

h2 {
  font-size: 24px;
}

h3 {
  font-size: 20px;

  .md-block({
    font-size: 18px;
    font-weight: 700;
  });
}

h4 {
  font-size: 16px;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.hidden {
  display: none !important;
}

body *:focus {
  outline: none;
}


input:focus {
  outline: none;
}
button:focus {
  outline: none;
}

