.ui-datepicker {
  border: 1px solid #E8EBF0 !important;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.08) !important;
}

.ui-datepicker .ui-datepicker-calendar {
  font-size: 14px;
  color: @lightDark;
  font-weight: 500;
}

.ui-datepicker .ui-datepicker-calendar {
  td {
    text-align: center;
    padding: 0;
  }
  td > a {
    display: inline-block;
    background-color: transparent;
    border: none;
    width: 28px;
    height: 28px;
    border-radius: 4px;
    line-height: 28px;
    padding: 0;
    text-align: center;
  }

  td:hover > a {
    background-color: @lightGrey;
  }

  .ui-datepicker-current-day a {
    display: inline-block;
    background-color: transparent;
    border: none;
    width: 28px;
    height: 28px;
    border-radius: 4px;
    line-height: 28px;
    padding: 0;
    text-align: center;
    color: @lightDark;
    background-color: @lightGrey;
  }

  .ui-datepicker-unselectable span {
    display: inline-block;
    background-color: transparent;
    border: none;
    width: 28px;
    height: 28px;
    border-radius: 4px;
    line-height: 28px;
    padding: 0;
    text-align: center;
    color: @grey;
  }
}

.ui-datepicker .ui-datepicker-next {
  transform: rotate(-90deg);

  & > .ui-icon {
    background-image: url("../img/icon-arrow-down.svg");
  }
}
.ui-datepicker .ui-datepicker-prev {
  transform: rotate(90deg);

  & > .ui-icon {
    background-image: url("../img/icon-arrow-down.svg");
  }
}
