
.direct-cart {
  width: 100%;
}

.direct-cart__title {
  margin-bottom: 32px;
}

.direct-cart__alarm {
  margin-bottom: 16px;
  .lg-block({
    display: none;
  });
}

.direct-cart__form {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: flex-start;
  max-height: 800px;

  .lg-block({
    max-height: none;
    flex-wrap: nowrap;
    align-items: stretch;
  });
}

.direct-cart__info {
  box-sizing: border-box;
  width: 62.8%;
  // flex-basis: 200px;
  background-color: #ffffff;

  .lg-block({
    width: 100%;
  });
}

.direct-cart__info.route-info {
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border: 1px solid @lightGrey;
  border-bottom: none;
}

.driver-info {
  border-left: 1px solid @lightGrey;
  border-right: 1px solid @lightGrey;
}

.direct-cart__info--passengers {
  box-sizing: border-box;
  padding: 32px 48px;
  width: auto;
  background-color: #ffffff;
  border-radius: 4px;
  border: 1px solid @lightGrey;
  order: 1;
  margin-right: 0;
  margin-left: auto;

  & > div:first-child {
    margin-bottom: 28px;
  }

  .lg-block({
    order: 0;
    margin: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;

    border-radius: 0;
    border-top: none;
    border-bottom: none;
  });

}

.direct-cart__footer {
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 48px;
  width: 62.8%;
  background-color: #ffffff;
  // flex-basis: 200px;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  border: 1px solid @lightGrey;
  border-top: none;

  .lg-block({
    width: 100%;
  });
}

.direct-cart__price {
  margin-left: auto;
  margin-right: 24px;
}

.direct-cart__reset {
  color: @red;
  // border-radius: 0;
  span {
    padding: 0;
    border-bottom: 1px solid @red;
  }

  &:hover {
    border-bottom-color: #E6320E;
  }

}


.direct-cart--desktop {
  .md-block({
    display: none;
  });
}



.direct-cart--mobile {
  display: none;

  .direct-cart__info {
    padding: 16px;
  }

  .route-info__to-from {
    margin: 0;
    margin-bottom: 45px;
  }

  .route-info__city span:last-child {
    width: 237px;
    bottom: -33px;

    .md-block({
      width: 215px;
    });
  }

  .route-info__button-map {
    color: @grey;
    border-bottom-color: @grey;
  }

  .route-info__addit-info {
    padding: 24px 16px;
    padding-bottom: 0;
    background-color: #ffffff;
    border-left: 1px solid @lightGrey;
    border-right: 1px solid @lightGrey;

    & > div:first-child {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 10px;
    }

    & > div:last-child {
      border-bottom: 1px solid @lightGrey;
      padding-bottom: 25px;
    }
  }

  .route-info__addit-places-left {
    font-size: 14px;
    font-weight: 500;

  }

  .btn-price__price {
    margin-bottom: 0;
    margin-right: 5px;
  }

  .route-info__companies {
    padding: 0;
  }

  .driver-info__inner {
    display: block;
    & > div:first-child {
      display: flex;
      margin-bottom: 26px;

      .driver-info__avatar {
        margin-right: 16px;
      }

      .driver-info__write-driver {
        margin: 0;
      }
      & > div:last-child {
        & > span {
          font-size: 16px;
          font-weight: 500;
        }
      }
    }
    .driver-info__column {
      margin-left: 80px;
      margin-bottom: 20px;
    }

    .driver-info__icons {
      margin-left: 60px;
    }
  }

  .direct-cart__footer {
    & > .button {
      // display: block;
      margin: 0 auto;
    }
  }

  .md-block({
    display: block;
  });
}

.direct-cart--booking-375 {
  display: none;
  margin: 0 auto;
  max-width: 520px;

  .md-block({
    display: block;
  });

  .direct-cart__info {
    display: block;
    padding: 24px 16px;
    border-top: 1px solid @lightGrey;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
  }
  .direct-cart__footer {
    padding: 24px 16px 32px 16px;
    border-top: none;
  }

  .add-passengers {
    // margin: 0;
    width: 100%;
    box-sizing: border-box;
    padding-right: 60px;
    padding-bottom: 26px;
    border-bottom: 1px solid @lightGrey;
  }

  .additional-params {
    padding-bottom: 30px;
    border-bottom: 1px solid @lightGrey;
  }

  .direct-cart__alarm {
    display: block;
    padding: 16px;
    margin-top: 24px;

    .alarma__icon {
      display: none;
    }

    .alarma__message,
    .alarma__description {
      font-size: 14px;
      padding: 0;
      margin: 0;
      border: none;
      width: 100%;
    }

    .alarma__message {
      margin-bottom: 8px;
    }
    .alarma__description {
      color: @grey;
    }
  }
}
