
.btn-price {
  width: 141px;
  display: flex;
  flex-direction: column;
}

.btn-price__price {
  margin-bottom: 8px;
  padding-right: 5px;
  text-align: right;
  line-height: 1em;
  font-weight: 500;
  font-size: 18px;
  color: @dark;

  span:last-child {
    font-size: 14px;
    font-weight: 400;
    color: @grey;
  }
}
