
.driver-edit-transf {
  box-sizing: border-box;
  max-width: 520px;
  border-radius: 4px;
  margin: 0 auto;
  background-color: #ffffff;
  border: 1px solid @lightGrey;

  padding: 24px 16px 32px 16px;

}

.driver-edit-transf__form {
  box-sizing: border-box;
}


.driver-edit-transf__left-col {
  width: 100%;
}

.driver-edit-transf__field:first-child {
  display: flex;
  flex-direction: column;
}

.driver-edit-transf__subtitle {
  margin-bottom: 16px;
}

.driver-edit-transf__view-map {
  display: inline-block;
  align-self: center;
  margin-top: 15px;
  margin-bottom: 32px;

  &--range {
    margin-bottom: 0;
  }

  .filter-search__range {
  height: 30px;
  display: flex;
  align-items: flex-end;
  padding-bottom: 32px;
  margin-bottom: 32px;
  border-bottom: 1px solid @lightGrey;

  .range__value-line {
    left: 0;
  }

  .range__pin {
    left: calc(55% - 10px);
  }

  .range__price-values {
    margin-left: 16px;
    margin-bottom: 8px;
  }
}
}



.driver-edit-transf__date-time {
  display: block;
  margin-bottom: 24px;

  & > div:last-child {
    box-sizing: border-box;
    width: 100%;
  }

  & > div:first-child {
    margin-bottom: 8px;
  }

}

.driver-edit-transf__right-col {

  p {
    font-size: 14px;
    color: @grey;
  }
}

.driver-edit-transf__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;

  p {
    font-size: 14px;
    color: @dark;
    text-align: left;
  }
}

.driver-edit-transf__text {
  width: 100%;
  margin-bottom: 16px;
  padding-top: 24px;
  border-top: 1px solid @lightGrey;
}

.driver-edit-transf__buttons {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column-reverse;
  padding-top: 24px;
}

.driver-edit-transf__reset {
  box-sizing: border-box;
  padding-left: 0;
  padding-right: 0;
  margin-top: 24px;
}

.driver-edit-transf__submit {
  box-sizing: border-box;

}
