
.auto-slider {

}

.auto-slider__big {
  overflow: hidden;
  margin-bottom: 24px;
  height: 288px;
  border-radius: 4px;

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .lg-block({
    height: 310px;
    margin-bottom: 16px;
  });
  .md-block({
    height: 205px;
  });
}

.auto-slider__carousel {
  position: relative;
  width: 321px;
  margin: 0 auto;


  .owl-item > div {
    box-sizing: border-box;
    width: 91px;
    height: 64px;
    border-radius: 4px;
    overflow: hidden;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  .md-block({
    width: 198px;

    // .owl-item  {
    //   margin-right: 18px;
    // }
  });
}

.auto-slider__control {
  position: absolute;
  top: 0;
  bottom: 0;
  border-radius: 0;
  display: block;
  border: none;
  background-color: transparent;
  padding: 0;
  margin: 0;
  width: 24px;
  cursor: pointer;

  span {
    display: block;
    width: 24px;
    height: 24px;
    pointer-events: none;
    position: absolute;
    top: calc(50% - 12px);
    left: calc(50% - 12px);
    background-image: url("../img/icon-arrow-down.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 130%;
    transition-duration: 0.4s;
  }


  &--prev {
    left: -48px;
    span {
      transform: rotate(90deg);
    }
  }

  &--next {
    right: -48px;
    span {
      transform: rotate(-90deg);
    }
  }

  &:hover {
    span {
      background-size: 150%;
    }
  }
}
