.user-menu__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.user-menu__item {
  margin-left: 24px;
  font-size: 16px;

  & > a {
    color: inherit;
    text-decoration: none;
  }

  .md-block({
    &:not(:last-child) {
      display: none;
    }
  });
}

