
.alarma {
  padding: 8px 48px;
  display: flex;
  align-items: center;
  border: 1px solid @yellow;
  border-radius: 4px;
  background-color: #ffffff;
}

.alarma__message {
  padding-left: 16px;
  padding-right: 22px;

  font-family: "Geometria", Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 500;
  color: @dark;

  a {
    color: @blue;
    border-bottom-color: @blue;
  }
}

.alarma__description {
  align-self: stretch;
  margin-left: auto;
  width: 260px;
  padding-left: 22px;
  border-left: 1px solid @lightGrey;

  font-size: 14px;
  color: @lightDark;
}
