
.offer-list {
  display: block;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  background-color: #ffffff;
  border: 1px solid @lightGrey;
}

.offer-list__item {
  padding: 12px 33px 12px 25px;
  display: flex;
  align-items: flex-start;
  font-size: 16px;
  color: @lightDark;
  line-height: 19px;

  &:hover {
    background-color: @lightGrey;
  }
}

.offer-list__icon {
  display: block;
  flex-shrink: 0;
  margin-right: 12px;
  margin-top: 2px;
  width: 16px;
  height: 16px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

}

.offer-list__item {

  &--map .offer-list__icon {
    background-image: url("../img/map-marker-alt-solid.svg");
  }
  &--plane .offer-list__icon {
    background-image: url("../img/plane-solid.svg");
  }
  &--hotel .offer-list__icon {
    background-image: url("../img/hotel-solid.svg");
  }
  &--architect .offer-list__icon {
    background-image: url("../img/architect-solid.svg");
  }
}
