.auto-short {
  display: flex;
  align-items: center;
  border-bottom: 1px solid @lightGrey;
}

.auto-short__photo {
  width: 48px;
  height: 48px;
  border-radius: 4px;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.auto-short__info {
  padding-left: 16px;
}

.auto-short__name {
  font-size: 16px;
  margin-bottom: 5px;
}

.auto-short__spec {
  font-size: 16px;
  color: @grey;
  span {
    padding-right: 16px;
  }
}
