
.filter-search {
  width: 256px;
  box-sizing: border-box;
  background-color: #ffffff;

  padding: 24px;
  padding-bottom: 30px;
  background-color: @white;
  border-radius: 4px;
  border: 1px solid @lightGrey;
  .lg-block({
    // position: relative;
  });
}

.filter-search__overlay {
  display: none;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: @dark;
  opacity: 0.75;
}

.filter-search__close {
  display: none;
  position: absolute;
  width: 24px;
  height: 24px;
  right: -40px;
  top: -24px;
  background-image: url("../img/close-icon.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.filter-search__wrapper {
  position: relative;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

.filter-search__title {
  margin-bottom: 24px;
  font-size: 18px;
  color: @dark;
}


.filter-search__inner {
  display: flex;
  flex-direction: column;

  & > .field:not(:last-child) {
    margin-bottom: 24px;
  }
}

.filter-search__field {
  display: flex;
  flex-direction: column;
  padding: 0;
  margin-bottom: 24px;
  font-size: 14px;
  font-weight: 500;
  color: @dark;

}

.filter-search__field-name {
  margin-bottom: 8px;
  line-height: 16px;
}



.filter-search__check {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  font-size: 14px;
  line-height: 1em;
  font-weight: 400;
  color: @dark;


  & > span:last-child {
    margin-left: 35px;
    padding-left: 6px;
    position: relative;

    &::before {
      content: "";
      display: block;
      position: absolute;
      width: 24px;
      height: 24px;
      top: calc(50% - 12px);
      left: -24px;
      background-position: center;
      background-repeat: no-repeat;
    }
  }

  &--smoke > span:last-child::before {
    background-image: url("../img/icon-smoking.svg");
  }
  &--pets > span:last-child::before {
    background-image: url("../img/icon-pets.svg");
  }
  &--music > span:last-child::before {
    background-image: url("../img/icon-music.svg");
  }

  &--color {
    & > span:last-child::before {
      height: 8px;
      top: calc(50% - 4px);
      border-radius: 4px;
    }
    &.red-- > span:last-child::before {
      background-color: @red;
    }
    &.blue-- > span:last-child::before {
      background-color: @blue;
    }
    &.black-- > span:last-child::before {
      background-color: #000000;
    }
  }
}

.filter-search__submit {
  margin-top: auto;
  margin-bottom: 15px;
  align-self: center;
}

.filter-search__reset {
  align-self: center;
  color: @blue;
  border-bottom: 1px solid @blue;
}

