.review {
  display: flex;
  align-items: flex-start;
}

.review__inner:first-child {
  margin-right: 8px;
  flex-shrink: 0;
}

.review__author-photo {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.review__author-name {
  font-size: 14px;
  font-weight: 500;

}

.review__service-info {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
}

.review__date {
  font-size: 12px;
  color: @grey;
  margin-right: 24px;

  .md-block({
    margin-right: 8px;
  });
}

.review__stars {
  display: inline-block;
  width: 106px;
  height: 16px;
  box-shadow: 0 0 0 1px red;
  margin-right: 8px;

  .md-block({
    margin-left: auto;
  });
}

.review__total {
  font-size: 12px;
  font-weight: 500;
}

.review__text {
  font-size: 16px;
}
