.block-2 {
  display: flex;
  position: relative;
  background-color: #ffffff;
  margin-bottom: 80px;

  .lg-block({
    margin-bottom: 60px;
  });

  .md-block({
    padding-top: 48px;
    flex-direction: column;
    // width: 375px;
    margin: 0 auto;
    margin-bottom: 48px;
  });
}

.block-2__wrapper {
  padding-top: 80px;
  padding-bottom: 80px;
  width: 50%;


  .md-block({
    display: flex;
    flex-direction: column;
  });

  & > div {
    width: 100%;

    .lg-block({
      width: 100%;
    });

    .md-block({
      width: 100%;
      display: flex;
      flex-direction: column;
    });
  }

  .lg-block({
    width: 48%;
    padding-bottom: 64px;
    padding-top: 64px;
  });

  .md-block({
    width: 100%;
    padding-top: 0;
  });
}

.block-2__background {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  overflow: hidden;

  & > ul {
    display: flex;
    & > li {
      margin-left: 32px;
    }
  }

  .lg-block({
    width: 48%;
  });

  .md-block({
    width: 100%;
    margin-bottom: 24px;
    justify-content: center;
  });
}

.block-2__title {
  margin-bottom: 24px;
  font-size: 32px;
  line-height: 1em;
  color: @dark;

  .lg-block({
    font-size: 24px;
  });
}

.block-2__text {
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 400;
  color: @lightDark;

  .md-block({
    margin-bottom: 24px;
  });
}

.block-2__button {
  .md-block({
    align-self: center;
    margin-left: auto;
    margin-right: auto;
  });
}


.block-2__car {
  box-sizing: border-box;
  padding-top: 40px;
  padding-bottom: 24px;
  width: 264px;
  border-radius: 4px;
  border: 1px solid @lightGrey;
  display: flex;
  flex-direction: column;
  align-items: center;

  &:first-child {
    opacity: 0.5;
  }

  & > h3 {
    font-size: 16px;
    text-align: center;
    margin-bottom: 18px;

    .md-block({
      margin: 0;
    });
  }

  .block-2__car-info {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
    font-size: 13px;

    & > span:first-child {
      margin-right: 16px;
    }

    .md-block({
      margin: 0;
    });
  }

  & > p {
    font-size: 14px;
    font-weight: 500;
    color: @grey;
  }

  .lg-block({
    width: 266px;

    &:nth-child(2) {
      opacity: 0.5;
    }
  });

  .md-block({
    width: 184px;
    height: 190px;
    justify-content: space-between;
    padding-top: 14px;
    padding-bottom: 14px;
    // padding: 0;

    &:nth-child(2) {
      opacity: 1;
    }

    &:last-child {
      opacity: 0.5;
    }
  });
}

.block-2__car-image {
  width: 100%;
  height: 90px;
  overflow: hidden;
  margin-bottom: 24px;


  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .md-block({
    height: 60px;
    margin: 0;
  });
}
