.tags__list {
  list-style: none;
  display: flex;
  margin: -4px;
  padding: 4px;
}

.tags__item {
  display: inline-block;
  margin: 4px;
  padding: 5px 12px;
  font-size: 11px;
  font-weight: 500;
  line-height: 1em;
  color: @lightDark;
  text-align: center;
  text-transform: uppercase;
  background-color: @lightGrey;
  border-radius: 50px;

}
