.range__values {
  display: flex;
  justify-content: space-between;
  align-items: center;
  & > span {
      display: block;
      width: 12px;
      height: 1px;
      margin: 0 1px;
      background-color: @lightDark;
    }
}

.range__values input {
  box-sizing: border-box;
  max-width: 88px;
  text-align: center;
  padding-left: 0;
  padding-right: 0;

}

.range__price-values {
  display: flex;
  justify-content: flex-start;
  align-items: center;

  & > span {
    display: block;
    width: 12px;
    height: 1px;
    margin: 0 7px;
    background-color: @lightDark;
  }

  & input {
    box-sizing: border-box;
    width: 80px;
    text-align: center;
    padding: 7px 7px;
    margin: 0;
  }
}

// range-1

.range-1 {
  border-radius: 0 !important;
  border: none !important;
  height: 4px !important;
  background-color: #E8EBF0 !important;

  .ui-slider-range {
    background-color: @lightGreen;
    border-radius: 0 !important;
  }

  .ui-slider-handle {
    width: 20px !important;
    height: 20px !important;
    border-radius: 50%;
    background-color: @green !important;
    top: calc(50% - 10px) !important;
    border: none !important;

    &::after {
      content: "";
      display: block;
      position: absolute;
      width: 6px;
      height: 6px;
      border-radius: 50%;
      top: calc(50% - 3px);
      left: calc(50% - 3px);
      background-color: #ffffff;
    }
  }
}

