.button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;

  box-sizing: border-box;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 14px 24px 15px 24px;
  border-radius: 24px;
  border: 1px solid #000000;

  font-size: 16px;
  line-height: 19px;
  text-decoration: none;
  text-align: center;
  color: inherit;

  span {
    text-align: center;
    font-weight: 500;
  }

  &--with-arrow {
    font-size: 14px;
    line-height: 16px;
    padding-top: 16px;
    padding-bottom: 16px;
    border-color: @green;
    color: @green;

    span {
      position: relative;
      margin: 0;
      padding-right: 20px;
    }
    span::after {
      content: "";
      position: absolute;
      display: block;
      width: 12px;
      height: 24px;
      right: 0;
      top: calc(50% - 12px);
      background-image: url("../img/icon-next.svg");
      background-repeat: no-repeat;
      background-position: center;
    }

    &:hover,
    &:focus {
      background-color: @green;
      color: #ffffff;
      span::after {
        background-image: url("../img/icon-next-white.svg");
      }
    }

    &.disabled,
    &:disabled {
      border-color: @lightGreen;
      color: @lightGreen;
      background-image: url("../img/icon-next-lightGreen.svg");
      cursor: default;
    }

  }

  &--fill-green {
    background-color: @green;
    border-color: @green;
    color: #ffffff;

    &:hover,
    &:focus {
      background-color: #27B02B;
      border-color: #27B02B;
    }

    &:disabled,
    &.disabled {
      background-color: @lightGrey;
      border-color: @lightGrey;
      color: #ffffff;
      cursor: default;
    }
  }

  &--fill-red {
    background-color: @red;
    border-color: @red;
    color: #ffffff;

    &:hover,
    &:focus {
      border-color: #E6320E;
      background-color: #E6320E;
    }

    &:disabled,
    &.disabled {
      border-color: @lightGrey;
      background-color: @lightGrey;
      color: #ffffff;
      cursor: default;
    }
  }

  &--outline-green {
    border-color: @green;
    color: @green;

    &:hover,
    &:focus {
      background-color: @green;
      color: #ffffff;
    }

    &:disabled,
    &.disabled {
      background-color: @lightGreen;
      border-color: @lightGreen;
      color: #ffffff;
      cursor: default;
    }
  }

  &--outline-red {
    border-color: @red;
    color: @red;

    &:hover,
    &:focus {
      background-color: @red;
      color: #ffffff;
    }
    &:disabled,
    &.disabled {
      border-color: #E59A8A;
      background-color: #E59A8A;
      color: #ffffff;
      cursor: default;
    }
  }

  &--outline-white {
    border-color: @white;
    color: @white;

    &:hover,
    &:focus {
      color: @lightDark;
      background-color: @lightGrey;
    }

    &:disabled,
    &.disabled {
      color: @grey;
      border-color: @grey;
    }
  }

  &--outline-dark {
    border-color: @lightGrey;
    color: @lightDark;

    &:hover,
    &:focus {
      background-color: @lightGrey;
      color: @lightDark;
    }

    &:disabled,
    &.disabled {
      color: @grey;
      border-color: @grey;
    }
  }
  &--outline-dark-v2 {
    border-color: @lightGrey;
    color: @lightDark;

    &:hover,
    &:focus {
      background-color: @lightGrey;
      color: @lightDark;
    }

    &:disabled,
    &.disabled {
      color: @grey;
      border-color: @grey;
    }
  }

  &--with-icon {
    // box-sizing: border-box;
    // height: 48px;
    // display: flex;
    // justify-content: center;
    // align-items: center;
    border-color: @lightGrey;
    // padding: 0 24px 0 48px;
    color: @lightDark;
    background-color: @white;

    span {
      position: relative;
      padding-left: 20px;
    }

    span::before {
      content: "";
      position: absolute;
      display: block;
      width: 16px;
      height: 24px;
      left: 0;
      top: calc(50% - 12px);
      background-image: url("../img/filter-solid.svg");
      background-repeat: no-repeat;
      background-position: center;
    }

    &:hover,
    &:focus {
      background-color: @lightGrey;
    }
  }

  &--lined {
    display: inline-block;
    border: none;
    border-radius: 0;
    padding: 0;
    font-size: 14px;
    color: @grey;
    border-bottom: 1px solid @grey;
    text-align: left;
    // span {
    //   margin: 0;
    // }

    &:hover,
    &:focus {
      color: @lightDark;
      border-bottom-color: @lightDark;
    }
  }

  &--lined-icon {
    position: relative;
    padding-left: 30px;
    border: none;
    border-radius: 0;
    font-size: 14px;
    color: @blue;
    text-align: left;
    &::before {
      content: "";
      display: block;
      width: 20px;
      height: 20px;
      position: absolute;
      top: calc(50% - 10px);
      left: 0;
      background-repeat: no-repeat;
      background-position: center;
    }
  }

  &--arrow-down {
    width: 20px;
    height: 20px;
    border: none;
    padding: 0;
    margin: 0;
    background-image: url("../img/icon-arrow-down.svg");
    background-repeat: no-repeat;
    background-position: center;
  }
}

.home-page .button--header {
  color: @white;
  border-color: @white;
  padding: 8px 24px;
  span {
    line-height: 24px;
    margin: 0 10px;
    font-weight: 500;
  }

  .md-block({
    border: none;
    padding: 0;
    span {
      font-size: 18px;
      display: flex;
      align-items: center;
      color: @dark;
    }

    span::before {
      content: "";
      display: inline-block;
      margin-right: 8px;
      width: 24px;
      height: 24px;
      background-image: url("../img/login-button.svg");
      background-repeat: no-repeat;
      background-position: center;
    }
  });

  &:focus,
  &:hover {
    background-color: @white;
    color: @lightDark;
  }
}

.inner-page .button--header {
  border-color: @lightDark;
  color: @lightDark;
  padding: 8px 24px;
  span {
    line-height: 24px;
    margin: 0 10px;
    font-weight: 500;
  }

  .md-block({
    border: none;
    padding: 0;
    span {
      font-size: 18px;
      display: flex;
      align-items: center;
      color: @dark;
    }

    span::before {
      content: "";
      display: inline-block;
      margin-right: 8px;
      width: 24px;
      height: 24px;
      background-image: url("../img/login-button.svg");
      background-repeat: no-repeat;
      background-position: center;
    }
  });
}


.button--lined-chat {
  color: @blue;
  padding-left: 0;
  &::before {
    background-image: url("../img/icon-comment.svg");
  }
}

.button--lined-companies {
  color: @blue;
  padding-left: 0;
  &::before {
    background-image: url("../img/icon-users-company-blue.svg");
  }
 }

 .button--txt-icon {
   padding: 0;
   margin: 0;
   border: none;
   text-decoration: none;
   display: flex;
   align-items: center;
   font-size: 14px;
   font-weight: 500;

   span {
    margin: 0;
    padding-right: 8px;
    padding-left: 8px;
   }

   svg {
     width: 20px;
     height: 20px;

   }

   &--blue {
    color: @blue;

     svg path {
     fill: @blue;
    }

    &:hover,
    &:focus {
      color: #7FB8FE;
      svg path {
        fill: #7FB8FE;
      }
    }
   }

   &--red {
    color: @red;

    svg path {
      fill: @red;
     }

     &:hover,
     &:focus {
       color: #E6320E;
       svg path {
         fill: #E6320E;
       }
     }
   }

 }
