.booking-transfer-card {
  display: block;
  height: 173px;
  border-radius: 4px;
  background-color: #ffffff;
  border: 1px solid @lightGrey;

  .md-block({
    height: auto;
    padding-bottom: 24px;
  });

  .pass-bag__pass,
  .pass-bag__bag {
    &::before {
      width: 24px;
      height: 24px;
    }
  }
}

.booking-transfer-card__content {
  display: flex;
  height: 128px;
  align-items: center;
  border-bottom: 1px solid @lightGrey;
  padding-left: 32px;
  // background-color: red;

  .lg-block({
    padding-left: 16px;
  });

  .md-block({
    height: auto;
    flex-direction: column;
    align-items: stretch;
    padding-left: 0;
    border-bottom: none;
  });
}

.booking-transfer-card__to-from {
  margin-right: 24px;
  .cards-direct__cities {
    width: 206px;

    .lg-block({
      width: 174px;
    });
  }

  .lg-block({
    margin-right: 16px;
  });

  .md-block({
    margin-right: 16px;
    margin-left: 16px;
    margin-bottom: 24px;
    padding-bottom: 24px;
    border-bottom: 1px solid @lightGrey;
  });
}

.booking-transfer-card__status {
  position: relative;
  margin-left: auto;
  border-top-right-radius: 4px;
  align-self: stretch;
  span {
    display: none;
    font-size: 16px;
  }

  &::after {
    content: "";
    border-top-right-radius: 4px;
    display: block;
    width: 32px;
    height: 100%;
    left: calc(50% - 12px);
    top: calc(50% - 12px);
    // background-color: @green;

    background-position: center;
    background-repeat: no-repeat;
    background-size: 24px;
    // background-image: url("../img/checked-white.svg");
  }

  .field__dropdown-contain {
    left: -360px;
  }

  &:hover > .field__dropdown-contain .field__drop-out { margin-top: 16px !important; }

  &--confirmed {
    span {
      color: @green;
    }

    &::after {
      background-color: @green;
      background-image: url("../img/checked-white.svg");
    }
  }
  &--awaiting {
    span {
      color: @yellow;
    }

    &::after {
      background-color: @yellow;
      background-image: url("../img/waiting.svg");
    }
  }
  &--canceled {
    span {
      color: @red;
    }

    &::after {
      background-color: @red;
      background-image: url("../img/canceled.svg");
    }
  }

  .lg-block({

    &::after {
      width: 26px;
      background-size: 18px;
    }
  });

  .md-block({
    order: -1;
    align-self: flex-end;
    display: flex;
    align-items: center;
    margin-bottom: 16px;
    // height: 32px;
    span {
      display: inline;
      padding-right: 8px;
    }

    &::after {
      height: 32px;
      width: 32px;
    }

    .field__dropdown-contain {
      display: none;
    }
  });

}

.booking-transfer-card__driver {
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  &::before,
  &::after {
    content: "";
    display: block;
    width: 1px;
    height: 96px;
    background-color: @lightGrey;
  }

  &::before {
    margin-right: 32px;
  }
  &::after {
    margin-left: 32px;
  }

  .lg-block({
    &::before {
      margin-right: 16px;
    }
    &::after {
      margin-left: 16px;
    }
  });

  .md-block({
    display: none;
  });

  .driver-info__rating {
    margin-top: -5px;
    .lg-block({
      display: none;
    });
  }
}

.booking-transfer-card__info {
  .md-block({
    display: none;
  });
}

.booking-transfer-card__addit {
  display: flex;

  .lg-block({
    flex-direction: column;
    flex-shrink: 0;
    margin-left: auto;
    margin-right: auto;
  });

  .md-block({
    display: none;
  });
}

.booking-transfer-card__places {
  // margin-left: auto;
  // margin-right: auto;
  .lg-block({
    margin-bottom: 5px;
  });
}

.booking-transfer-card__places-list {
  display: flex;
  flex-wrap: wrap;
  width: 96px;



  // .md-block({
  //   display: none;
  // });
  .lg-block({
    &:nth-child(2) .booking-transfer-card__subtitle {
      margin-bottom: 0;
    }
  });
}

.booking-transfer-card__subtitle {
  margin-bottom: 5px;


  .lg-block({
    font-size: 12px;
    color: @grey;
  });
}

.booking-transfer-card__price {
  margin-left: 48px;

  .lg-block({
    margin-left: 0;

    h4 {
      margin-bottom: 0;
    }
  });
}
.booking-transfer-card__price-value {
  font-size: 14px;
  line-height: 24px;
  font-weight: 500;
  color: @dark;

  span {
    color: @grey;
    font-weight: 400;
    font-family: "Roboto Condensed", Arial, Helvetica, sans-serif;
  }

}

.booking-transfer-card__footer {
  height: 44px;
  display: flex;
  justify-content: flex-end;
}


.booking-transfer-card__links {
  width: 60%;
  box-sizing: border-box;
  padding: 0 42px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  .lg-block({
    width: 85%;
    padding: 0 16px;
    padding-left: 42px;
  });

  .md-block({
    display: none;
  });
}

