.booking-transfer-card__more {
  display: none;
  .md-block({
    display: block;
    margin: 0 auto;
  });
}

.booking-transfer-card__status-drop {
  box-sizing: border-box;
  width: 100%;
  padding: 24px;
  li {
    display: flex;
    align-items: center;
    margin-bottom: 12px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  svg {
    margin-right: 8px;
  }
  .booking-transfer-card__status-drop-text {
    display: block;
    font-size: 12px;
    color: @lightDark;
  }
}


// 4-1

.booking-transfer-card--4_1 {

  .md-block({

    .booking-transfer-card__driver {
        display: block;
        box-sizing: border-box;
        width: auto;
        margin-left: 16px;
        margin-right: 16px;

        &::before,
        &::after {
          display: none;
        }
      }

    .booking-transfer-card__to-from {
      display: none;
    }

    .booking-transfer-card__more {
      display: flex;
      margin-right: 0;
      padding-right: 16px;

      .button {
        box-sizing: border-box;
        width: 141px;
      }
    }

  });

  .booking-transfer-card__more-addit {
    display: none;
    font-size: 14px;
    font-weight: 500;
    color: @grey;
    text-align: right;
    margin-right: 16px;

    & > span {
      display: block;
    }

    .booking-transfer-card__addit-price {
      font-size: 16px;
      color: @dark;
      padding-right: 16px;

      span {
        padding-left: 4px;
        font-size: 14px;
        color: @grey;
        font-weight: 400;
        font-family: "Roboto Condensed", Arial, Helvetica, sans-serif;
      }
    }

    .md-block({
      display: block;
    });
  }

}

.booking-transfer-card--6_3 {

  .lg-block({
    .booking-transfer-card__status {
      display: none;
    }
  });

  .md-block({
    .booking-transfer-card__to-from {
      margin-top: 24px;
    }
  });

}
